@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*slick sliders*/
// arrows
.slick-arrow {
	width: 40px;
	height: 40px;
	display: block;
	position: absolute;
	border: none;
	border-radius: 0;
	background: none;
	outline: none;
	box-shadow: none;
	opacity: 1;
	z-index: 6;
	transition-property: background, opacity, visibility;
	transition-duration: 0.2s;

	.no-touchevents &:hover {
		opacity: 1;
		background: #e6e6e6; }

	&.slick-disabled, .no-touchevents &.slick-disabled {
		opacity: 0.25;
		-ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
		cursor: default;
		background: none;

		&::before {
			opacity: 1; } }

	&::before {
		content: none; }

	&::after {
		top: 50%;
		content: "";
		position: absolute;
		opacity: 1;
		pointer-events: none;
		width: 12px;
		height: 12px;
		transform: rotate(45deg);
		border: 0 solid #000; } }

.slick-prev {
	left: 0;

	&::after {
		left: 50%;
		border-width: 0 0 2px 2px;
		margin: -6px 0 0 -4px; } }

.slick-next {
	right: 0;

	&::after {
		right: 50%;
		border-width: 2px 2px 0 0;
		margin: -6px -4px 0 0; } }

// arrows outside
.slick-arrow-outside, .slick-arrow-outside:hover, .slick-arrow-outside:focus {
	background-color: $base-color; }

//         
.slick-arrow-outside {
	left: auto;
	@include size(30px);

	.no-touchevents &:hover, .no-touchevents &:focus {
		background-color: tint($base-color, 70%);

		&.slick-disabled {
			background-color: $base-color; } }

	&::after {
		border-color: white; } }

.slick-prev-outside {
	right: (30px + 10); }

.slick-next-outside {
	right: 0; }


// dots
.slick-dots {
	margin: 0;
	line-height: 0;

	.#{$user-content} & {
		margin: 0;
		max-width: none; }

	li {
		vertical-align: top;
		width: auto;
		height: auto;
		margin: 0;

		&, .#{$user-content} & {
			padding: 4px 10px;
			margin: 0;

			&::before {
				content: none; } }

		button {
			width: 12px;
			height: 12px;
			padding: 0;
			border-radius: 50%;
			background: $text-color-alt;
			position: relative;
			transition-property: background, border, box-shadow, transform;
			transition-duration: 0.6s;

			&::before {
				content: '';
				position: absolute;
				left: 2px;
				top: 2px;
				height: 8px;
				width: 8px;
				background: #fff;
				border-radius: 50%;
				opacity: 1;
				box-shadow: 0 2px 3px rgba(0, 0, 0, 0.20);
				transition-property: transform, opacity;
				transition-duration: $animation-speed; } }

		&.slick-active button::before {
			opacity: 0; } } }

//.no-touchevents &:hover button::before
/*slick sliders end*/

/*images slider*/
.images-slider {
	position: relative;
	margin-bottom: $bottom-space;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--lg; } }

.images-slider__list, .slick-dotted.images-slider__list {
	padding-bottom: $bottom-space;
	margin-bottom: 0;
	position: relative;
	z-index: 2; }

.images-slider__item {
	float: none;
	display: inline-block;
	vertical-align: top;
	opacity: 0;
	white-space: normal;
	overflow: hidden;
	position: relative;
	background: #494949;

	.slick-initialized & {
		display: block;
		float: left;
		opacity: 1; }

	.no-js & {
		display: block;
		width: 100%;
		opacity: 1; }

	&::before {
		content: '';
		display: block;
		height: 0;
		padding-top: 62.5%; }

	img {
		position: absolute;
		top: -2px;
		left: 50%;
		text-align: center;
		max-width: none;
		height: calc(100% + 4px);
		transform: translate(-50%, 0); } }

.images-slider {

	.slick-arrow {
		right: 0;
		left: auto;
		top: 100%;
		transform: translate(0, 0); }

	.slick-prev {
		right: 40px; }

	// slider counter
	.slider-counter {
		position: absolute;
		left: 0;
		top: 100%;
		color: $text-color-light;

		.slide-curr {
			color: $text-color; } }

	.slick-dots, .#{$user-content} & .slick-dots {
		position: absolute;
		bottom: $bottom-space + 1;

		li {
			@include padding-horizontal(7px);

			button {
				width: 6px;
				height: 6px;
				border-radius: 50%;
				background-color: white;
				opacity: 0.5;
				transition-property: opacity, transform;
				transition-duration: $animation-speed;

				&::before {
					content: none; } }

			&.slick-active button {
				opacity: 1; }

			.no-touchevents &:hover button {
				opacity: 1;
				transform: scale(2); } } } }

/*images slider end*/

/*flashes*/
.flashes {
	padding: 0 90px; }

.flashes__item {
	@include font(1.4rem, 1.5);
	color: $text-color-light;

	& > *:last-child {
		margin-bottom: 0; } }

/*flashes end*/
