@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*FOOTER*/
.footer {

	.layout {
		position: relative; }

	.footer__holder {
		overflow: hidden;
		position: absolute;
		top: auto;
		right: auto;
		bottom: 0;
		cursor: default;
		border-top: 1px solid $outline-color; } }

//.footer__holder
//verflow: hidden
//ursor: default
//adding: 30px 0

/*footer common*/
.footer-common {
	position: relative;
	overflow: hidden;
	background: $bg-color;
	color: black;

	a:not([href^="tel"]) {
		@include font(inherit, inherit);
		color: inherit;
		text-decoration: none;
		transition-property: color;
		transition-duration: $animation-speed;

		.no-touchevents &:hover {
			color: $link-color; } }

	.copyright {
		font-size: 13px;
		text-transform: uppercase; }

	.dl {
		list-style: none;

		li {
			margin-bottom: 0; }

		.dt, .dd {
			@include display-inline-block-top; }

		.hide {
			display: none; } }

	.dev {
		@include font-normal;
		font-size: 14px;
		color: $text-color-light; } }

.footer-common-holder {
	float: left;
	width: 100%; }

// footer nav
.footer-nav {
	margin: 0 round(-$footer-nav-horizontal-space*0.5);
	@include clear-spacing; }

.footer-nav__col {
	position: relative;
	@include display-inline-block-top;
	width: round-float(100%/5, 2);
	padding: 0 $footer-nav-horizontal-space*0.5;
	margin-bottom: $footer-nav-horizontal-space;
	@include font(16px, 24px);
	letter-spacing: normal;

	@include media('<desk-sm') {
		width: round-float(100%/4, 2);

		&.footer-add-info {
			width: 100%; } }

	@include media('<tab-lg') {
		width: round-float(100%/2, 2);
		margin-bottom: 20px;

		&.footer-add-info {
			width: 100%; } }

	@include media('<mob-lg') {
		display: none;
		width: 100%;

		&.footer-add-info {
			display: block;
			text-align: center;
			margin-bottom: 0; } }

	& > *:last-child {
		margin-bottom: 0; }

	.ea-site &.footer-add-info {
		display: block;
		width: auto;
		margin-bottom: 0; } }

.footer-nav__title {
	position: relative;
	display: block;
	@include font-alt-bold;
	font-size: 18px;
	color: black;
	margin-bottom: 1.5em;
	padding-bottom: 1.5em;

	&::after {
		@include pseudo();
		bottom: 0;
		border-top: 2px solid black;
		width: 32px; }

	@include media('<tab-lg') {
		margin-bottom: 0.5em;
		padding-bottom: 0.5em; }

	a {
		color: inherit;
		text-decoration: none;

		& > span {
			border-bottom: 1px solid transparent;
			transition-property: border-color;
			transition-duration: $animation-speed; }

		.no-touchevents &:hover > span {
			border-bottom-color: rgba(255, 255, 255, 0.30); } } }

.footer-nav__links {
	list-style: none;
	margin: 0 0 1.5em;
	color: $text-color-light; }

.footer-nav__list {
	list-style: none;
	@include font-alt-bold;
	font-size: 18px;
	color: black; }

.footer-nav__links, .footer-nav__list {

	li {
		margin-bottom: 20px;
		overflow: hidden;

		@include media('<tab-lg') {
			margin-bottom: 0.5em; }

		&:last-child {
			margin-bottom: 0; } }

	a {
		padding-top: 0.3em;
		color: inherit;
		@include font(inherit, inherit);
		text-decoration: none;
		transition-property: color;
		transition-duration: $animation-speed;

		.no-touchevents &:hover {
			color: $link-color; } } }

.footer-add-info {
	.ea-site & {
		@include clear-spacing;
		margin: 0 -#{round($footer-nav-horizontal-space/2)}; } }

.footer-nav__item {
	.ea-site & {
		@include display-inline-block-top;
		width: 50.0%;
		margin-bottom: 20px;
		overflow: hidden;
		padding: 0 #{round($footer-nav-horizontal-space/2)};
		@include font(16px, round-float(24/16, 2));
		letter-spacing: normal;

		&:first-child {
			display: block;
			width: auto; }

		&:last-child {
			text-align: right; }

		@include media('<mob-lg') {
			display: block;
			text-align: center;
			width: auto;

			&:last-child {
				text-align: center;
				margin-bottom: 0; } } } }

/*footer common end*/

