@import "../utils/_variables";
@import "../utils/_mixins";
@import "../../libs/include-media/dist/_include-media";

/* goods */
.goods {
	overflow: hidden;
	margin-bottom: $bottom-space--md;

	@include media('<mob-lg') {
		margin-bottom: 2rem; }

	&:last-child {
		margin-bottom: 0; } }

.goods__caption {
	$info-size: 60px;
	$info-size--sm: 60px;

	padding-right: #{$info-size + 20px};
	margin-bottom: 5rem;
	min-height: $info-size;

	@include media('<mob-lg') {
		padding-right: 0;
		margin-bottom: 3rem; }

	&:last-child {
		margin-bottom: 0; }

	h3 {
		@include font($font-size-h3, $line-height-h3);
		margin-bottom: 0;

		@include media('<mob-lg') {
			@include font($font-size-h3--sm, $line-height-h3--sm);
			margin-bottom: 2rem; } }

	.info {
		display: block;
		position: absolute;
		right: 0;
		top: 0;
		@include size($info-size);
		padding: 11px 4px 4px;
		border: 1px solid #{$text-color};
		@include font(8px, 12px);
		letter-spacing: normal;
		text-transform: uppercase;
		text-decoration: none;
		color: $text-color;
		text-align: center;
		cursor: pointer;
		transition-property: background-color;
		transition-duration: $animation-speed;

		.no-touchevents &:hover {
			background-color: $bg-color-light; }

		@include media('<mob-lg') {
			position: relative;
			right: auto;
			top: auto;
			@include push-auto; }

		&::before {
			@include pseudo-block-relative();
			margin: 0 auto 8px;
			@include size(9px, 19px);
			background-image: url('../../img/depict-info.png');
			background-repeat: no-repeat;
			background-position: center;
			background-size: contain;
			backface-visibility: hidden; } } }

.goods__list {
	@include clear-spacing;
	padding-bottom: 1px; }

.goods__item {
	@include display-inline-block-top;
	width: 100%;
	position: relative;
	padding-top: 1px;
	@include font(1.5rem, round-float(18px/15px, 2));
	background-color: #{$bg-color};
	letter-spacing: normal; }

.goods__inner {
	display: block;
	@include padding-vertical(13px);
	text-decoration: none;
	color: inherit;
	@include clear-spacing;
	transition-property: background-color;
	transition-duration: $animation-speed;

	.no-touchevents & {
		&:hover, &:focus {
			background: $bg-color-light; } } }

//     safari
// ..        flex-box 
//  ,       
.goods, .goods__list, .goods__item, .goods__inner {
	position: relative;

	&::before, &::after {
		@include pseudo();
		@include size(0);
		border: 0 solid $outline-color; } }

.goods {
	&::after {
		left: 0;
		bottom: 0;
		border-width: 0 0 1px;
		@include size(9999px, 0); } }

.goods__list, .goods__item {
	&::before {
		right: -1px;
		top: 0;
		@include size(0, 9999px);
		border-width: 0 1px 0 0;
		display: none; } }

.goods__list {
	&::after {
		top: 0;
		left: 0;
		@include size(9999px, 0);
		border-width: 0 0 1px 0; } }

.goods__item {
	&::after {
		top: 0;
		left: 0;
		@include size(0, 9999px);
		border-width: 0 0 0 1px;
		display: none; } }

.goods__inner {
	&::after {
		top: -1px;
		left: 0;
		@include size(9999px, 0);
		border-width: 1px 0 0; } }

//     safari END

.goods__figure, .goods__content, .goods__title, .goods__text {
	@include display-inline-block-middle; }

$product-figure-width: 90px;
$product-figure-right-space: 20px;
.goods__figure {
	position: relative;
	width: #{$product-figure-width};
	margin-right: #{$product-figure-right-space};
	letter-spacing: normal;

	&:before {
		@include pseudo-block-relative();
		@include ratio(1, 1); } }

.goods__img {
	@include cover-abs();
	font-size: $font-size;
	letter-spacing: normal;
	backface-visibility: hidden;

	img {
		display: block;
		max-height: 100%;
		max-width: 100%;
		@include centering-transform-rel(); } }

.goods__content {
	width: calc(100% - (#{$product-figure-width} + #{$product-figure-right-space}));

	ul {

		li, ul, ol {
			margin: 0.7em 0; }

		li {
			padding-left: 0;

			&::before {
				content: none; } }

		a {
			padding-top: 0.2em;
			color: inherit;
			text-decoration: none;
			transition-property: color;
			transition-duration: $animation-speed;

			.no-touchevents &:hover {
				color: $link-color; } }

		ul, ol {
			padding-left: 30px;

			li {
				&::before {
					content: '';
					display: inline-block;
					position: relative;
					margin-right: 5px;
					@include position(auto, auto);
					width: auto;
					height: auto;
					border: none; } } } } }

.goods__title {
	width: 20%;
	@include font-alt-bold;
	padding-right: 40px;
	@include font(16px, 19px);
	letter-spacing: normal;
	transition-property: color;
	transition-duration: $animation-speed;

	@include media('<desk-lg') {
		width: 30%; }

	@include media('<desk-md') {
		width: 40%; }

	a {
		text-decoration: none;
		color: inherit;
		transition: inherit;
		padding-top: 0.2em;

		.no-touchevents &:hover {
			color: $link-color; } } }

.goods__text {
	position: relative;
	@include font($font-size--sm, $line-height--sm);
	letter-spacing: normal;
	width: 79%;

	& > *:last-child {
		margin-bottom: 0; }

	@include media('<desk-lg') {
		width: 69%; }

	@include media('<desk-md') {
		width: 59%; } }

.goods__footer {
	text-align: center; }

//   
@include media('<tab-lg') {
	.goods {
		padding-right: 1px; }

	.goods__list {
		display: flex;
		flex: 0 1 auto;
		flex-flow: row wrap; }

	.goods__item {
		@include display-inline-block-top;
		padding-left: 1px;
		width: round-float(100%/2, 4); }

	.goods__inner {
		padding: 30px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 100%;

		@include media('<mob-lg') {
			padding: 20px; } }

	.goods__list, .goods__item {
		&::before {
			display: block; } }

	.goods__item {
		&::after {
			display: block; } }

	.goods__body {
		display: block;
		width: auto;
		@include padding-horizontal(0); }

	.goods__figure, .goods__content, .goods__title, .goods__text {
		display: block;
		width: auto; }

	.goods__figure {
		margin: 0 auto $bottom-space;
		width: 100%;
		max-width: 100px; }

	.goods__title {
		margin-bottom: 0.5em;
		padding: 0;

		//    
		text-align: center; }

	.goods__text {
		margin-bottom: 1em;

		//    
		text-align: center; } }

.goods__item {
	@include media('<mob-lg') {
		width: round-float(100%/1, 4); } }

/* grid view end */
/* goods end */
