@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*HEADER*/

.header {
	background-color: white;
	box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1); }

.header__align {
	width: calc(100% + 100px);
	overflow-x: hidden;
	overflow-y: auto;
	height: 100vh; }

.header__holder {
	//display: table
	//min-height: 100vh //    
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	min-height: 100vh; // ie  min-height  table
	width: #{$header-width};

	//     display: flex
	.no-flexbox & {
		display: table;
		table-layout: fixed; }

	//   11   flex-direction: column   min-height
	//     
	@media all and (-ms-high-contrast: none) {
		&::-ms-backdrop, & {
			display: table;
			table-layout: fixed; } } }

.header__row {

	//     display: flex
	.no-flexbox & {
		display: table-row; }

	//   11   flex-direction: column   min-height
	//     
	@media all and (-ms-high-contrast: none) {
		&::-ms-backdrop, & {
			display: table-row; } } }

.header__row--logo {
	overflow: hidden;
	height: 0; //      flex: box  3 , -  display: none

	//.home-page &
	//eight: 1%

	@include media('<desk-lg') {
		height: 1%; } }

.header__row--arrow {
	overflow: hidden;
	height: 0; //      flex: box  3 , -  display: none

	.home-page & {
		height: 1%;

		//     display: flex
		.no-flexbox & {
			display: table-row;
			height: 1%; }

		//   11   flex-direction: column   min-height
		//     
		@media all and (-ms-high-contrast: none) {
			&::-ms-backdrop, & {
				display: table-row;
				height: 1%; } } } }

.header__cell {
	text-align: center;

	//     display: flex
	.no-flexbox & {
		display: table-cell;
		vertical-align: middle; }

	//   11   flex-direction: column   min-height
	//     
	@media all and (-ms-high-contrast: none) {
		&::-ms-backdrop, & {
			display: table-cell;
			vertical-align: middle; } }

	.header__row--logo & {
		vertical-align: top; }

	.header__row--arrow & {
		vertical-align: bottom;

		svg {
			display: block;
			width: 30px;
			height: 30px;
			@include push-auto;
			margin-bottom: 20px; } } }

/*logo*/
.logo {
	display: block;
	width: $header-width;
	z-index: 50;

	a {
		display: block;
		position: relative;
		height: 0;
		@include ratio(1, 1);
		background-color: $logo-bg-color;
		transition-property: background-color, opacity, transform, visibility;
		transition-duration: $animation-speed;
		z-index: 5;

		.ea-site & {
			background-color: transparent; }

		.shutter--before-open & {
			transform: translate(50%, 0);
			opacity: 0;
			visibility: hidden; }

		//&::before
		//include pseudo()
		//eft: 0
		//op: 0
		//include size($header-width+$nav-width, 100%)
		///background-color: $logo-bg-color
		//pacity: 0
		//isibility: hidden
		//ransition-property: transform, opacity, visibility, background-color
		//ransition-duration: $animation-speed
 } }		//-index: -1

.no-touchevents {
	.logo a:hover {
		background-color: lighten($logo-bg-color, 10%); }

	.ea-site .logo a:hover {
		background-color: #{$bg-color-light}; } }

.logo-img {
	display: block;
	position: absolute;
	@include position(0, 0);
	text-indent: -9999px;
	overflow: hidden;
	@include size($header-width);
	background: url(../../img/logo-short.png) no-repeat center center;
	background-size: auto 47px;
	transform:  scale(0.75);
	transition-property: transform, background-color;
	transition-duration: $animation-speed;
	backface-visibility: hidden;

	.ea-site & {
		background-image: url(../../img/logo-short--ea.png); } }

