@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*news grid */
.news-grid {
	margin-bottom: $bottom-space;

	&.news-grid--home {
		margin-bottom: 0; }

	&:last-child {
		margin-bottom: 0; } }

.news-grid__list {
	position: relative;
	@include grid-parent($grid-space);
	@include clear-spacing;

	@include media('<desk-hd') {
		@include grid-parent($grid-space--sm); } }

.news-grid__item {
	position: relative;
	letter-spacing: normal;

	.news-grid & {
		@include grid-item(3, $grid-space);
		letter-spacing: normal;

		@include media('<desk-hd') {
			@include grid-item(3, $grid-space--sm); }

		@include media('<tab-sm') {
			@include grid-item(1, $grid-space--sm); } } }

.news-grid__inner {
	display: block;
	position: relative;
	text-decoration: none;
	color: $text-color;
	background-color: #ddd;
	height: 100%; } // inner    item

.news-grid__content {
	padding: #{$grid-inside-vertical-space+8} $grid-inside-horizontal-space $grid-inside-vertical-space;

	@include media('<desk-hd') {
		padding: 28px 20px 20px; } }

.news-grid__meta {
	margin-bottom: 0.8em;
	padding-bottom: 0.8em;
	@include font(1.8rem, 1.3);
	border-bottom: 1px solid $border-color;

	&:last-child {
		margin-bottom: 0; }

	time {
		display: block;
		@include font-bold;
		color: $text-color; } }

.news-grid__title {
	@include font-normal;
	@include font(2.2rem, 1.227);
	color: $text-color;
	margin-bottom: 1.5em;

	&:last-child {
		margin-bottom: 0; } }

.news-grid__text {
	margin-bottom: $bottom-space;
	overflow: hidden;
	@include font(18px, 24px);
	max-height: calc(24px * 13);

	@include media('<desk-hd') {
		@include font(16px, 20px);
		max-height: calc(20px * 13); }

	@include media('<desk-sb') {
		max-height: calc(20px * 8); }

	@include media('<tab-lg') {
		max-height: calc(20px * 12); }

	//@media (max-height: 949px)
	//ax-height: calc(#{$font-size * $line-height} * 8)

	//@media (max-height: 769px)
	//ax-height: calc(#{$font-size * $line-height} * 6)

	//@media (max-height: 637px)
	//ax-height: calc(#{$font-size * $line-height} * 4)

	&:last-child {
		margin-bottom: 0; } }
