@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*FORM STYLES DEFAULT */
/*form layout */
.form-row, .input-wrap {
	position: relative; // !      
	margin-bottom: $form-bottom-space;
	z-index: 1; } // !      z-index: -1

.form-row:last-child {
	margin-bottom: 0; }

.input-holder {
	position: relative; // !      
	z-index: 1; } // !      z-index: -1

.form-group {
	margin-bottom: $bottom-space--md;

	&:last-child {
		margin-bottom: 0; } }

.form-group-title {
	@include font-bold;
	@include font(18px, round-float(32/18, 2));
	margin-bottom: $bottom-space; }

.form-row {
	@include margin-horizontal(-#{round($form-gap*0.5)});
	font-size: 0;
	letter-spacing: -4px;

	.input-wrap {
		@include display-inline-block-top;
		width: calc(100% * 0.5 - #{$form-gap});
		@include margin-horizontal(#{round($form-gap*0.5)});
		margin-bottom: 0;

		font-size: $font-size;
		letter-spacing: normal;

		@include media('<mob-lg') {
			display: block;
			width: auto;
			margin-bottom: $form-bottom-space--sm; } } }

label {
	@include font($font-size, $line-height);
	color: $text-color; }

.label-holder {
	@include font($form-font-size, $form-line-height);
	color: $text-color;
	margin-bottom: 2px;

	label {
		@include font(inherit, inherit);
		color: inherit; } }

.form-mark {
	display: inline-block;
	vertical-align: top;
	//color: $checkbox-color
	color: $text-color; }

/*form layout end*/

/* form footer */
.form-footer {
	@include clear;

	.form-buttons {
		@include display-inline-block-middle;
		margin-right: 20px; }

	.form-note {
		@include display-inline-block-middle;
		@include font(14px, 22px); } }

/* form footer end */

/*form common*/
input[type="text"], input[type="password"], input[type="email"], input[type="search"], textarea {
	display: block;
	padding: #{$form-input-space-v} #{$form-input-space-h};
	@include font-normal;
	@include size(100%, $form-input-height);
	@include font($form-font-size, $form-line-height);
	color: $form-text-color;
	letter-spacing: normal;
	background: none;
	//background: $form-bg-color
	box-shadow: inset 0 0 0 1px transparent;
	border: 1px solid $form-border-color;
	transition-property: border-color, background-color, box-shadow;
	transition-duration: $animation-speed;
	margin: 0 0 $form-bottom-space;

	.input-wrap & {
		margin: 0; }

	//form hover
	.no-touchevents &:hover {
		border-color: $form-border-focus-color; }

	.no-touchevents .ea-site &:hover {
		border-color: $base-color--ea; }

	//form focus
	&:focus {
		border-color: $form-border-focus-color;
		box-shadow: inset 0 0 0 1px $form-border-focus-color;

		.ea-site & {
			border-color: $base-color--ea;
			box-shadow: inset 0 0 0 1px $base-color--ea; } }

	//form error
	&.error, .no-touchevents &.error, .ea-site &.error, .no-touchevents .ea-site &.error {
		color: $form-error-color;
		border-color: $form-error-color;
		box-shadow: inset 0 0 0 1px transparent;

		&:focus {
			box-shadow: inset 0 0 0 1px $form-error-color; } } }

textarea {
	padding: #{$form-input-space-v} #{$form-input-space-h};
	min-height: 114px;
	height: 114px;
	resize: vertical; }

.error-note {
	position: absolute;
	@include size(100%, 22px);
	@include font(12px, 22px);
	letter-spacing: normal; // !
	@include truncate(none);
	color: $form-error-color;
	display: none;

	form .error ~ & {
		display: block; } }

.confirm-note {

	$this-icon-size: 16px;
	$this-icon-lines-width: 12px;
	$this-icon-lines-height: 2px;

	position: absolute;
	left: 100%;
	top: round($form-input-height*0.5);
	@include font(0, 0);
	@include size($this-icon-size);
	margin: -8px 0 0 #{round(($text-layout-space-horizontal - $this-icon-size)*0.2)};
	backface-visibility: hidden;
	z-index: -1;
	pointer-events: none;
	display: none;

	@include media('<tab-lg') {
		margin-left: #{round(($text-layout-space-horizontal--md - $this-icon-size)*0.2)}; }

	@include media('<tab-sm') {
		margin-left: #{round(($text-layout-space-horizontal--sm - $this-icon-size)*0.2)}; }

	form .success ~ & {
		display: block; }

	&::before, &::after {
		@include pseudo();
		bottom: 0;
		backface-visibility: hidden;
		background-color: $base-color;
		transition-property: background-color;
		transition-duration: $animation-speed;

		.ea-site & {
			background-color: $base-color--ea; } }

	&::before {
		@include size(round($this-icon-lines-width*0.75), $this-icon-lines-height);
		right: 50%;
		transform-origin: bottom right;
		transform: rotate(45deg); }

	&::after {
		@include size(round($this-icon-lines-width*1.3), $this-icon-lines-height);
		left: 50%;
		transform-origin: bottom left;
		transform: rotate(-55deg); } }

/*form common end*/

/*form placeholder*/
input, textarea {
	&::-webkit-input-placeholder {
		color: $form-placeholder-color;
		transition: $animation-speed; }

	&:-moz-placeholder {
		color: $form-placeholder-color;
		transition: $animation-speed; }

	&::-moz-placeholder {
		color: $form-placeholder-color;
		transition: $animation-speed;
		opacity: 1; }

	&:-ms-input-placeholder {
		color: $form-placeholder-color;
		transition: $animation-speed;
		opacity: 1; }

	&.placeholder {
		color: $form-placeholder-color;
		transition: $animation-speed;
		opacity: 1; }

	//form placeholder hide
	&:focus {
		&::-webkit-input-placeholder {
			color: transparent !important; }

		&:-moz-placeholder {
			color: transparent !important; }

		&::-moz-placeholder {
			color: transparent !important; }

		&:-ms-input-placeholder {
			color: transparent !important; } }

	&.placeholder:focus {
		color: transparent !important; }

	//form error placeholder
	&.error {
		&::-webkit-input-placeholder {
			color: $form-error-color; }

		&:-moz-placeholder {
			color: $form-error-color; }

		&::-moz-placeholder {
			color: $form-error-color;
			opacity: 1; }

		&:-ms-input-placeholder {
			color: $form-error-color;
			opacity: 1; }

		&.placeholder {
			color: $form-error-color;
			opacity: 1; } } }

/*form placeholder end*/

/*form checkbox and radiobutton*/
input[type="checkbox"] ~ label, input[type="radio"] ~ label, .check-label, .radio-label {
	@include display-inline-block-top;
	@include font(16px, 20px);
	color: #000;
	text-transform: none;
	cursor: pointer;
	position: relative;
	transition-property: color;
	transition-duration: $animation-speed;

	.no-touchevents &:hover {
		color: $link-color; } }

input[type="checkbox"],
input[type="radio"] {
	float: left;
	@include size(16px);
	padding: 0;
	margin: 0;
	display: none;

	& ~ label > span,
	& ~ span {
		display: inline-block;
		vertical-align: top;
		position: relative;
		padding: 0 0 0 34px;
		z-index: 2; }

	& ~ i {
		@include font(0, 0); }

	& ~ i {
		position: absolute;
		left: 0;
		top: 50%; } }

//checkbox
input[type="checkbox"] {

	& ~ label,
	& ~ i {
		&::before {
			content: '';
			@include size(18px);
			position: absolute;
			left: 0;
			top: 50%;
			margin-top: -9px;
			border-radius: 0;
			border: 1px solid $checkbox-color; }

		&::after {
			content: '';
			@include size(16px);
			position: absolute;
			left: 1px;
			top: 50%;
			margin-top: -8px;
			border-radius: 0;
			background: none;
			transition-property: opacity;
			transition-duration: $animation-speed; } }

	& ~ label > span,
	& ~ span {
		&::before,
		&::after {
			content: '';
			position: absolute;
			@include size(0, 3px);
			left: 0;
			top: 50%;
			border-radius: 2px;
			background: $checkbox-color;
			transition-property: opacity;
			transition-duration: $animation-speed;
			transition-timing-function: linear; }

		&::before {
			transform: rotate(45deg);
			transform-origin: 0 7px;
			z-index: 1; }

		&::after {
			transform: rotate(-50deg);
			transform-origin: 0 10px;
			left: 22px; } }

	// checkbox checked
	&:checked {
		& ~ label > span,
		& ~ span {
			&::before {
				width: 7px;
				margin: -4px 0 0 0;
				transition-property: width;
				transition-duration: 0.1s;
				backface-visibility: hidden; }

			&::after {
				width: 11px;
				margin: 0 0 0 -8px;
				transition-property: width;
				transition-duration: 0.1s;
				transition-delay: 0.1s;
				backface-visibility: hidden; } } }

	// checkbox disabled
	&:disabled {
		& ~ label,
		& ~ span {
			color: #bdbdbd;
			cursor: default; }

		& ~ label,
		& ~ i {
			&::before {
				border-color: #d6d6d6; } }

		& ~ label > span,
		& ~ span {
			&:before,
			&:after {
				background: #b9b9b9; } } } }

// radio
input[type="radio"] {

	& ~ label,
	& ~ span {
		&::before {
			content: '';
			@include size(18px);
			position: absolute;
			left: 0;
			top: 50%;
			margin-top: -9px;
			border-radius: 50px;
			border: 1px solid $checkbox-color; }

		&::after {
			content: '';
			@include size(16px);
			position: absolute;
			left: 1px;
			top: 50%;
			margin-top: -8px;
			border-radius: 50px;
			background: none;
			transition-property: opacity;
			transition-duration: 0.2s; } }

	& ~ label > span,
	& ~ i {
		&::after {
			content: '';
			@include size(10px);
			position: absolute;
			top: 50%;
			left: 4px;
			margin-top: -5px;
			border-radius: 50%;
			overflow: hidden;
			background: $checkbox-color;
			animation: bounceout 0s;
			animation-iteration-count: 1;
			animation-fill-mode: forwards;
			opacity: 0; } }

	// radio checked
	&:checked {
		& ~ label > span,
		& ~ i {
			&::after {
				animation: bounce 0.3s;
				animation-iteration-count: 1;
				opacity: 1; } } }

	@keyframes bounce {
		0% {
			transform: scale(0.8);
			opacity: 0.4; }

		50% {
			transform: scale(1.4);
			opacity: 1; }

		100% {
			transform: scale(1);
			opacity: 1; } }

	@keyframes bounceout {
		0% {
			transform: scale(0);
			opacity: 1; }

		50% {
			transform: scale(1.4);
			opacity: 1; }

		100% {
			transform: scale(0);
			opacity: 0.4; } }

	// radio disabled
	&:disabled {
		& ~ label,
		& ~ span {
			color: #bdbdbd;
			cursor: default;

			&::before {
				border-color: #d6d6d6; } }

		& ~ label > span,
		& ~ i {
			&::after {
				background: #b9b9b9; } } } }

.check-list {
	@include font(16px, 20px);

	&, .#{$user-content} & {
		margin: 0;
		padding: 0;

		li {
			margin: 0 0 20px;
			padding-left: 0;

			&::before {
				content: none; } } } }

/*form checkbox and radiobutton end*/

