/*select*/
// select overall
.select {
	position: relative;
	margin: 0 0 $form-bottom-space;
	width: 100%;

	.input-wrap & {
		margin-bottom: 0; }

	.cselect {
		width: 100%; }

	select,
	.select2-container--default .select2-selection {
		display: block;
		position: relative;
		overflow: hidden;
		height: $form-input-height;
		text-overflow: '';
		cursor: pointer;
		padding: #{$select-space-v} #{$select-arrow-width} #{$select-space-v} #{$select-space-h};
		white-space: nowrap;
		text-indent: 0;
		width: 100%;
		border: 1px solid $form-border-color;
		border-radius: 0;
		background: none;
		//background: $form-bg-color
		box-shadow: none;
		transition-property: background, border, color, box-shadow;
		transition-duration: $animation-speed;
		outline: none; }

	.select2-container--default .select2-selection--multiple {
		height: auto; }

	select,
	.select2-container--default .select2-selection .select2-selection__rendered {
		@include font-normal;
		@include font($form-font-size, $form-line-height);
		text-transform: none;
		color: $form-text-color; }

	select {
		padding-right: 40px;

		//&:focus
		//olor: $form-text-color
 }		//order-color: $form-border-focus-color

	.select2-container--default {
		.select2-selection--single {
			.select2-selection__rendered {
				padding-left: 0;
				padding-right: 0; }

			.select2-selection__arrow {
				position: absolute;
				right: 0;
				top: 0;
				@include size($select-arrow-width, $select-arrow-height);
				//background-color: white
				background-color: transparent;
				z-index: 2;

				b {
					border: none;
					@include cover-abs();
					margin: 0; } } } }

	&::before,
	&::after,
	.select2-container--default .select2-selection--single .select2-selection__arrow b::before,
	.select2-container--default .select2-selection--single .select2-selection__arrow b::after {
		content: '';
		position: absolute;
		right: 22px;
		top: 50%;
		@include size(8px, 2px);
		background-color: $title-color;
		margin-top: -1px;
		z-index: 1;
		backface-visibility: hidden; }

	&::before,
	.select2-container--default .select2-selection--single .select2-selection__arrow b::before {
		transform: rotate(45deg); }

	&::after,
	.select2-container--default .select2-selection--single .select2-selection__arrow b::after {
		transform: rotate(-45deg);
		right: 17px; }

	.select2-container--default .select2-search--inline .select2-search__field {
		height: auto;
		margin: 0;
		box-shadow: none; }

	// multiple
	.select2-container--default .select2-selection--multiple {
		padding: 8px #{$select-arrow-width} 8px #{$select-space-h};

		.select2-selection__rendered {
			padding-left: 0;
			padding-right: 0;
			margin: 0;
			vertical-align: top;
			max-width: none;

			li {
				padding: 0 4px;
				margin: 3px 8px 3px 0;
				border-right: 1px solid transparent;

				&::before {
					content: none; } } }

		.select2-selection__choice {
			background-color: #f3f3f3;
			border: 1px solid #f3f3f3;
			border-radius: 0;
			color: $text-color; }

		.select2-selection__choice__remove {
			color: $text-color;

			&:hover {
				color: $link-color; } } } }

.select select.error {
	&,
	& ~ .select2-container--default .select2-selection--single,
	& ~ .select2-container--default .select2-selection--multiple {
		border-color: $form-error-color; } }

/*select2 dropdown*/
.select2-dropdown {
	border-color: $form-border-color;
	border-radius: 0;
	z-index: 9999; }

.select2-results__options {
	@include font-normal;
	@include font($form-font-size, $form-line-height); }

.select2-results__option {
	padding: 8px #{$select-space-h} 8px #{$select-space-h}; }

.select2-container--default {
	.select2-search--dropdown {
		.select2-search__field {
			border-color: $form-border-color;
			margin: 0;
			height: 34px;

			&:focus {
				border-color: $form-border-focus-color;
				//box-shadow: inset 0 0 0 1px $form-border-focus-color
				box-shadow: none;

				//.ea-site &
 } } }				//order-color: $base-color--ea

	.select2-results>.select2-results__options {
		max-height: 200px; }

	.select2-results__option[aria-selected=true] {
		background-color: #f3f3f3; }

	.select2-results__option--highlighted[aria-selected] {
		background-color: $base-color;
		color: $text-color;

		.ea-site & {
			background-color: $base-color--ea; } } }

.select2-container--open {
	.select2-dropdown--below {
		box-shadow: 0 -1px 0 $form-border-color; }

	.select2-dropdown--above {
		box-shadow: 0 1px 0 $form-border-color; } }

/*select2 dropdown end*/
/*select end*/
/*FORM STYLE DEFAULT end*/
