@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*user form*/
.form-title {
	display: block;
	@include font-alt-bold;
	@include font($font-size-h2, $line-height-h2);
	color: $title-color;
	text-align: center;
	margin-bottom: $bottom-space--md; }

.user-form {
	background: $bg-color-alt;
	margin-bottom: $bottom-space--md;

	form {
		display: block; }

	.form-row, .input-wrap {
		margin-bottom: 40px;

		.input-wrap {
			margin-bottom: 0;

			@include media('<mob-lg') {
				margin-bottom: 40px; } } }

	textarea {
		height: 130px;
		max-height: 130px;
		resize: none; }

	.captcha {
		@include clear-spacing;

		@include clear;

		img {
			$margin-right: 20px;
			@include display-inline-block-middle;
			margin-right: $margin-right; }

		.input-holder {
			@include display-inline-block-middle;
			width: 200px; }

		@include media('<mob-lg') {
			text-align: center;

			img {
				display: block;
				margin: 0 auto $bottom-space--sm; }

 		@include clear; }

		@include media('<mob-lg') {
			padding-top: $bottom-space--sm; } }

	.form-buttons {
		text-align: center;

		.btn-submit {
			min-width: 216px; } }

	.form-note {
		overflow: hidden;
		min-height: 50px;
		@include clear-spacing;
		line-height: 25px;
		text-align: center;

		&::before {
			content: '';
			display: inline-block;
			vertical-align: middle;
			min-height: inherit; } }

	.form-note__item {
		display: inline-block;
		vertical-align: middle;
		width: calc(100% - 6px);
		@include font($font-size--less, $line-height--less);
		letter-spacing: normal; } }

/*user form end*/
