@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*LAYOUT*/
.wrapper {
	margin: 0 auto;
	//min-height: 100%
	//height: auto !important
	//height: 100%
	min-height: 100vh;
	overflow: hidden;
	position: relative; }

.main {
	padding: 0;
	position: relative;
	clear: both;
	z-index: 4; }

.main__holder {
	width: 100%;
	@include clear; }

.content-wrap {
	float: left;
	width: 100%; }

.content {
	//  ( )
	& {
		margin: 0 #{$sidebar-width} 0 #{$header-width};
		min-height: 50px; }

	//    ()
	.side-menu--alt & {
		margin-right: #{$sidebar-width-alt}; }

	//  ()
	.ea-site & {
		margin-right: #{$catalog-categories-width--full}; }

	//  ()   
	.ea-has-catalog-menu & {
		margin-right: #{$catalog-menu-width + $catalog-categories-width--full}; }

	@include media('<desk-qhd') {
		&, .ea-site & {
			margin-right: #{$sidebar-width}; }

		//  ()   
		.ea-has-catalog-menu & {
			margin-right: #{$catalog-menu-width + $catalog-categories-width--short}; }

		//  ()  
		.ea-site.home-page & {
			margin-right: #{$catalog-categories-width--full}; } }

	@include media('<desk-lg') {
		//  ()   
		.ea-has-catalog-menu & {
			margin-right: #{$catalog-menu-width}; }

		.side-menu--alt & {
			margin-right: #{$sidebar-width-alt--sm}; } }

	@include media('<desk-md') {
		//  ()   
		.ea-site &, .ea-site.home-page & {
			margin-right: 0; } }

	@include media('<tab-lg') {
		&, .side-menu--alt &, .ea-site &, .ea-site.home-page & {
			margin-right: 0;
			margin-left: 0; } } }

/* top panel (layout) */
.top-panel {
	$base-width: 100% - #{$header-width};

	//   ( )
	& {
		position: relative;
		z-index: 40;
		margin: 0 0 0 $header-width;
		width: calc(#{$base-width} - #{$sidebar-width}); }

	//     ()
	.side-menu--alt & {
		width: calc(#{$base-width} - #{$sidebar-width-alt}); }

	//    (   )
	&.top-panel--alt {
		position: fixed;
		//left: $header-width
		//width: calc(100% - #{$sidebar-width} - #{$header-width} - #{$catalog-categories-width--full})
 }		//margin: 0

	//   ()   
	.ea-site & {
		width: calc(#{$base-width} - #{$catalog-categories-width--full}); }

	//   ()   
	.ea-has-catalog-menu & {
		width: calc(#{$base-width} - #{$catalog-menu-width} - #{$catalog-categories-width--full}); }

	@include media('<desk-qhd') {
		&, .ea-site & {
			width: calc(#{$base-width} - #{$sidebar-width}); }

		//   ()   
		.ea-has-catalog-menu & {
			width: calc(#{$base-width} - #{$catalog-menu-width} - #{$catalog-categories-width--short}); }

		//   ()  
		.ea-site.home-page & {
			width: calc(#{$base-width} - #{$catalog-categories-width--full}); } }

	@include media('<desk-lg') {
		//   ()   
		.ea-has-catalog-menu & {
			width: calc(#{$base-width} - #{$catalog-menu-width}); }

		//     ()
		.side-menu--alt & {
			width: calc(#{$base-width} - #{$sidebar-width-alt--sm}); } }

	@include media('<desk-md') {
		//   ()    
		.ea-site &, .ea-site.home-page & {
			width: calc(#{$base-width}); } }

	@include media('<desk-md','>=tab-lg') {
		.ea-site.home-page &.top-panel--alt {
			position: relative;
			padding-top: #{$top-panel-height}; } }

	@include media('<tab-lg') {
		&, .ea-site &, .side-menu--alt &, .ea-site.home-page & {
			width: 100%;
			margin-left: 0;
			padding-top: #{$top-panel-height--md}; }

		&.top-panel--alt {
			position: relative; } } }

.top-panel-holder {
	position: relative;
	z-index: 5;
	height: $top-panel-height;
	padding: 0 #{$text-layout-space-horizontal};

	@include media('<desk-hd') {
		@include padding-horizontal($text-layout-space-horizontal--lg); }

	@include media('<desk-md','>=tab-lg') {
		.ea-site.home-page & {
			position: fixed;
			@include position(0, $header-width);
			width: calc(100% - #{$header-width}); } }

	@include media('<tab-lg') {
		position: fixed;
		@include position(0, 0);
		@include padding-horizontal(0);
		height: $top-panel-height--md;
		width: 100%;
		min-width: 320px;
		box-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);

		.top-panel--alt & {
			box-shadow: none; } } }

/*top panel (layout) end */

/* header (layout) */
.header {
	overflow: hidden;
	position: fixed;
	@include position(0, 0);
	@include size($header-width, 100%);
	z-index: 1001;

	@include media('<tab-lg') {
		display: none; } }

/* header (layout) end */

/* footer (layout) */
.footer {
	margin: 0 auto;
	position: relative;
	clear: both;
	z-index: 5;

	.home-page & {
		display: none;

		@include media('<tab-lg') {
			display: block; } }

	.footer__holder {
		$base-width: 100% - #{$header-width};

		//  ( )
		& {
			left: $header-width;
			width: calc(#{$base-width} - #{$sidebar-width}); }

		//    ()
		.side-menu--alt & {
			width: calc(#{$base-width} - #{$sidebar-width-alt}); }

		//  ()   
		.ea-site & {
			width: calc(#{$base-width} - #{$catalog-categories-width--full}); }

		//  ()   
		.ea-has-catalog-menu & {
			width: calc(#{$base-width} - #{$catalog-menu-width} - #{$catalog-categories-width--full}); }

		@include media('<desk-qhd') {
			&, .ea-site & {
				width: calc(#{$base-width} - #{$sidebar-width}); }

			//  ()   
			.ea-has-catalog-menu & {
				width: calc(#{$base-width} - #{$catalog-menu-width} - #{$catalog-categories-width--short}); }

			//  ()  
			.ea-site.home-page & {
				width: calc(#{$base-width} - #{$catalog-categories-width--full}); } }

		@include media('<desk-lg') {
			//  ()   
			.ea-has-catalog-menu & {
				width: calc(#{$base-width} - #{$catalog-menu-width}); }

			//    ()
			.side-menu--alt & {
				width: calc(#{$base-width} - #{$sidebar-width-alt--sm}); } }

		@include media('<desk-md') {
			//  ()    
			.ea-site &, .ea-site.home-page & {
				width: calc(#{$base-width}); } }

		@include media('<tab-lg') {
			&, .ea-site &, .side-menu--alt &, .ea-site.home-page & {
				left: 0;
				width: 100%; } } } }

/* footer (layout) end */

/* footer common (layout) */
.footer-common {
	padding: round($text-layout-space-horizontal*1.5) #{$text-layout-space-horizontal} round($text-layout-space-horizontal*1.5 - $footer-nav-horizontal-space);

	@include media('<desk-hd') {
		padding: round($text-layout-space-horizontal--lg*1.5) #{$text-layout-space-horizontal--lg} round($text-layout-space-horizontal--lg*1.5 - $footer-nav-horizontal-space); }

	@include media('<tab-lg') {
		padding: round($text-layout-space-horizontal--md*1.5) #{$text-layout-space-horizontal--md} 0; }

	@include media('<tab-sm') {
		padding: round($text-layout-space-horizontal--sm*1.5) #{$text-layout-space-horizontal--sm} 0; }

	@include media('<mob-lg') {
		padding: round($text-layout-space-horizontal--sm*1.5) #{$text-layout-space-horizontal--sm}; } }

/* footer common (layout) end */

/* catalog section (layout) */
.c-section__container {

	//      ( )
	&::before {
		@include pseudo-inline-block();
		vertical-align: middle;
		min-height: 100vh;

		@include media('<tab-lg') {
			content: none; } } }

.c-section__align {
	//      ( )
	@include display-inline-block-middle; }

.c-section__align, .main-section__align {

	padding: round($top-panel-height + $text-layout-space-horizontal) #{$text-layout-space-horizontal} round($text-layout-space-horizontal*1.5);

	@include media('<desk-hd') {
		padding: round($top-panel-height + $text-layout-space-horizontal--lg) #{$text-layout-space-horizontal--lg} round($text-layout-space-horizontal--lg*1.5); }

	@include media('<tab-lg') {
		//   .top-panel   
		padding: round($text-layout-space-horizontal--md*3) #{$text-layout-space-horizontal--md}; }

	@include media('<tab-sm') {
		//   .top-panel   
		padding: round($text-layout-space-horizontal--sm*3) #{$text-layout-space-horizontal--sm}; } }

.ea-main-section__align, .ea-contacts__panel {
	@include padding-horizontal(#{$text-layout-space-horizontal});
	padding-bottom: round($text-layout-space-horizontal*1.5);

	@include media('<desk-hd') {
		@include padding-horizontal(#{$text-layout-space-horizontal--lg});
		padding-bottom: round($text-layout-space-horizontal--lg*1.5); }

	@include media('<tab-lg') {
		@include padding-horizontal(#{$text-layout-space-horizontal--md});
		padding-bottom: round($text-layout-space-horizontal--md*1.5); }

	@include media('<tab-sm') {
		@include padding-horizontal(#{$text-layout-space-horizontal--sm});
		padding-bottom: round($text-layout-space-horizontal--sm*1.5); } }

// if has background image or map
.ea-main-section--entry, .ea-main-section--news {
	&, & + .ea-main-section {
		.ea-main-section__align {
			padding-top: round($text-layout-space-horizontal*1.5);

			@include media('<desk-hd') {
				padding-top: round($text-layout-space-horizontal--lg*1.5); }

			@include media('<tab-lg') {
				padding-top: round($text-layout-space-horizontal--md*1.5); }

			@include media('<tab-sm') {
				padding-top: round($text-layout-space-horizontal--sm*1.5); } } } }

.ea-contacts__panel {
	padding-top: round($text-layout-space-horizontal*1.5);

	@include media('<desk-hd') {
		padding-top: round($text-layout-space-horizontal--lg*1.5); }

	@include media('<tab-lg') {
		padding-top: round($text-layout-space-horizontal--md*1.5); }

	@include media('<tab-sm') {
		padding-top: round($text-layout-space-horizontal--sm*1.5); } }

.ea-contacts__thumbs {
	@include padding-horizontal(#{$text-layout-space-horizontal});
	top: round($text-layout-space-horizontal*1.5);

	@include media('<desk-hd') {
		@include padding-horizontal(#{$text-layout-space-horizontal--lg});
		top: round($text-layout-space-horizontal--lg*1.5); }

	@include media('<tab-lg') {
		@include padding-horizontal(#{$text-layout-space-horizontal--md});
		top: round($text-layout-space-horizontal--md*1.5); }

	@include media('<tab-sm') {
		@include padding-horizontal(#{$text-layout-space-horizontal--sm});
		top: round($text-layout-space-horizontal--sm*1.5); } }

/* catalog section (layout) end */

/* total date (layout) */
.total-data, .odds {
	padding: $text-layout-space-horizontal;
	padding-top: 0;

	@include media('<desk-hd') {
		padding: $text-layout-space-horizontal--lg;
		padding-top: 0; }

	@include media('<tab-lg') {
		padding: $text-layout-space-horizontal--md;
		padding-top: 0; }

	@include media('<tab-sm') {
		padding: $text-layout-space-horizontal--sm;
		padding-top: 0; } }

/* total date (layout) end */

/* catalog menu (layout) */
.catalog-menu {
	//overflow: hidden
	position: fixed;
	right: 0;
	top: 0;
	height: 100%;
	width: $sidebar-width;
	z-index: 991;

	.ea-has-catalog-menu & {
		width: $catalog-menu-width + $catalog-categories-width--full;

		@include media('<desk-qhd') {
			width: $catalog-menu-width + $catalog-categories-width--short; }

		@include media('<desk-lg') {
			width: $catalog-menu-width; } }

	&, .ea-has-catalog-menu & {

		@include media('<desk-md') {
			width: $catalog-menu-width;
			z-index: 998; }

		@include media('<tab-lg') {
			width: 100%;
			min-width: 320px; } } }

/* catalog menu (layout) end */

/* side menu (layout) */
/* side menu (layout) end */

.home-page .spacer {
	@include media('>=tab-lg') {
		display: none; } }

.max-wrap {
	max-width: $page-layout-width; }

.layout {
	@include padding-horizontal($page-layout-space-horizontal);
	@include push-auto;

	.main &, .header & {
		padding: 0; }

	@include media('<tab-lg') {
		.main & {
			@include padding-horizontal(0); } } }

.max-wrap-text {
	max-width: $text-layout-width; }

//      () 
//    
.layout-text {
	max-width: $text-layout-width + $text-layout-space-horizontal*2;
	@include padding-horizontal($text-layout-space-horizontal);
	@include push-auto;

	@include media('<desk-hd') {
		@include padding-horizontal($text-layout-space-horizontal--lg); }

	@include media('<tab-lg') {
		@include padding-horizontal($text-layout-space-horizontal--md); }

	@include media('<tab-sm') {
		@include padding-horizontal($text-layout-space-horizontal--sm); } }

//      () 
//    
.max-wrap-flood {
	max-width: $flood-layout-width; }

.layout-flood {
	max-width: $flood-layout-width + $text-layout-space-horizontal*2;
	@include padding-horizontal($flood-layout-space-horizontal);
	@include push-auto;

	@include media('<desk-hd') {
		@include padding-horizontal($text-layout-space-horizontal--lg); }

	@include media('<tab-lg') {
		@include padding-horizontal($text-layout-space-horizontal--md); }

	@include media('<tab-sm') {
		@include padding-horizontal($text-layout-space-horizontal--sm); } }

//       
//     ( )
.max-wrap-wide {
	max-width: $wide-layout-width; }

.layout-wide {
	@if $wide-layout-width {
		max-width: $wide-layout-width + $text-layout-space-horizontal*2; }
	@include padding-horizontal($flood-layout-space-horizontal);
	@include push-auto;

	@include media('<desk-hd') {
		@include padding-horizontal($text-layout-space-horizontal--lg); }

	@include media('<tab-lg') {
		@include padding-horizontal($text-layout-space-horizontal--md); }

	@include media('<tab-sm') {
		@include padding-horizontal($text-layout-space-horizontal--sm); } }

//       (   .layout-article)
//    
.layout-article {
	padding: round($text-layout-space-horizontal) #{$text-layout-space-horizontal} round($text-layout-space-horizontal*1.5);

	@include media('<desk-hd') {
		padding: round($text-layout-space-horizontal--lg) #{$text-layout-space-horizontal--lg} round($text-layout-space-horizontal--lg*1.5); }

	.ea-has-catalog-menu & {
		@include media('<desk-md') {
			padding-top: round($text-layout-space-horizontal--lg); } }

	@include media('<tab-lg') {
		padding: round($text-layout-space-horizontal--md*1.5) #{$text-layout-space-horizontal--md} round($text-layout-space-horizontal--md*2);

		.ea-has-catalog-menu & {
			padding-top: round($text-layout-space-horizontal--md); } }

	@include media('<tab-sm') {
		padding: round($text-layout-space-horizontal--sm*1.5) #{$text-layout-space-horizontal--sm} round($text-layout-space-horizontal--sm*2);

		.ea-has-catalog-menu & {
			padding-top: round($text-layout-space-horizontal--sm); } }

	p, ul, ol, h2, h3, h4, h5, h6, hr, .layout-text {
		max-width: $text-layout-width;
		@include padding-horizontal(0);
		@include push-auto; }

	.layout-flood {
		max-width: $flood-layout-width;
		@include padding-horizontal(0);
		@include push-auto;

		p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
			max-width: none; } }

	//      
	.layout-wide {
		max-width: $wide-layout-width;
		@include padding-horizontal(0);
		@include push-auto; }

	//     
	.layout-full-width {

		@include margin-horizontal(-#{$text-layout-space-horizontal});

		@include media('<desk-hd') {
			@include margin-horizontal(-#{$text-layout-space-horizontal--lg}); }

		@include media('<tab-lg') {
			@include margin-horizontal(-#{$text-layout-space-horizontal--md}); }

		@include media('<tab-sm') {
			@include margin-horizontal(-#{$text-layout-space-horizontal--sm}); }

		p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
			max-width: none; } } }

.user-form {
	padding: $text-layout-space-horizontal;

	@include media('<desk-hd') {
		padding: $text-layout-space-horizontal--lg; }

	@include media('<tab-lg') {
		padding: $text-layout-space-horizontal--md; }

	@include media('<tab-sm') {
		padding: $text-layout-space-horizontal--sm;
		border-width: 1px 0; } }

/* text popup (layout) */
.text-popup-layout {
	padding: 0 #{$text-layout-space-horizontal--lg} #{round($text-layout-space-horizontal--lg)};

	@include media('<tab-lg') {
		@include padding-horizontal(#{$text-layout-space-horizontal--md});
		padding-bottom: #{$text-layout-space-horizontal--md}; }

	@include media('<tab-sm') {
		@include padding-horizontal(#{$text-layout-space-horizontal--sm});
		padding-bottom: #{$text-layout-space-horizontal--sm}; } }

.text-popup-heading {
	@include margin-horizontal(-#{$text-layout-space-horizontal--lg});

	@include media('<tab-lg') {
		@include margin-horizontal(-#{$text-layout-space-horizontal--md}); }

	@include media('<tab-sm') {
		@include margin-horizontal(-#{$text-layout-space-horizontal--sm}); } }

/* text popup (layout) end */

/*LAYOUT end*/

/* browserupgrade */
.old-ie {
	overflow: hidden;

	body {
		overflow: hidden; } }

.browserupgrade {
	position: fixed;
	height: 100%;
	width: 100%;
	padding: 0.2em 0;
	margin: 0;
	@include font(28px, 42px);
	color: #000;
	background: #ccc;
	z-index: 99999;
	zoom: 1; }

.browserupgrade p {
	position: absolute;
	width: 100%;
	top: 50%;
	margin: -22px 0 0;
	padding: 0;
	text-align: center;
	vertical-align: middle; }

/* browserupgrade end */
