@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* shutter */
html.css-scroll-fixed {

	&.touchevents {
		@include scroll-blocked(); } } //       ( )

.content-wrap, .scroll-nav, .top-panel, .footer__holder {
	transition-property: transform;
	transition-duration: $animation-speed;
	transition-timing-function: cubic-bezier(0.65, 0.05, 0.36, 1) !important;

	.shutter--before-open & {
		transform: translate($nav-width - 250, 0);

		@include media('<tab-lg') {
			transform: none; } } }

//.side-menu
//shutter--before-open &
//transform: translate($sidebar-drop-width, 0)
//transition-timing-function: cubic-bezier(0.65, 0.05, 0.36, 1)

//@include media('<tab-lg')
//	transform: none

.shutter {
	overflow: hidden; //    
	position: fixed;
	@include position(0, #{$header-width});
	@include size(100%, 100vh);
	min-width: 320px;
	max-width: $nav-width;
	//padding: 0 0 120px
	background: $bg-color-light;
	opacity: 0;
	visibility: hidden;
	z-index: 1000;

	@include media('<tab-lg') {
		left: 0;
		//top: #{$top-panel-height--md}
		//height: calc(100vh - #{$top-panel-height--md})
		max-width: none;
		padding-top: 0; } }

