.shutter__align {
	width: calc(100% + 100px);
	overflow-x: hidden;
	overflow-y: auto;
	height: 100vh;

	@include media('<tab-lg') {
		width: 100%; } }

.shutter__holder {
	//display: table
	//min-height: 100vh //    
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	min-height: 100vh; // ie  min-height  table
	width: #{$nav-width};

	@include media('<tab-lg') {
		width: 100%; }

	//     display: flex
	.no-flexbox & {
		display: table;
		table-layout: fixed; }

	//   11   flex-direction: column   min-height
	//     
	@media all and (-ms-high-contrast: none) {
		&::-ms-backdrop, & {
			display: table;
			table-layout: fixed; } } }

.shutter__row {

	//     display: flex
	.no-flexbox & {
		display: table-row; }

	//   11   flex-direction: column   min-height
	//     
	@media all and (-ms-high-contrast: none) {
		&::-ms-backdrop, & {
			display: table-row; } } }

.shutter__cell {
	//     display: flex
	.no-flexbox & {
		display: table-cell;
		vertical-align: middle; }

	//   11   flex-direction: column   min-height
	//     
	@media all and (-ms-high-contrast: none) {
		&::-ms-backdrop, & {
			display: table-cell;
			vertical-align: middle; } } }

/* shutter header */
.shutter-header {
	position: relative;
	border: 0 solid darken($bg-color-light, 20%);

	@include media('<tab-lg') {
		text-align: center;
		padding: 15px #{$text-layout-space-horizontal--md + $top-panel-height--md} 15px #{$text-layout-space-horizontal--md};
		border-bottom-width: 1px;

		//.home-page &
 } }		//ackground-color: $logo-bg-color

/* shutter header end */

/* shutter footer */
.shutter-footer {
	//position: absolute
	//left: 0
	//bottom: 0
	//width: 100%
	padding: 0 #{$text-layout-space-horizontal--lg} 30px;
	@include font(1.2rem, 1.6);
	color: $text-color;

	@include media('<tab-lg') {
		@include padding-horizontal($text-layout-space-horizontal--md);
		text-align: center; }

	@include media('<tab-sm') {
		@include padding-horizontal($text-layout-space-horizontal--sm); }

	.copyright {
		margin-bottom: 1em;
		padding-bottom: 1em;
		@include font(1.4rem, 1.5);
		text-transform: uppercase;
		border-bottom: 1px solid $border-color;

		&:last-child {
			margin-bottom: 0;
			padding-bottom: 0;
			border: none; } }

	.map-site {
		color: $text-color-light;

		a {
			color: inherit;
			text-decoration: none;
			transition-property: color;
			transition-duration: $animation-speed;

			.no-touchevents &:hover {
				color: $base-color; } } } }

/* shutter footer end */

/* button shutter close */
.shutter {
	.btn-popup-close {
		display: none;
		position: absolute;
		right: 0;
		top: 0;

		@include media('<tab-lg') {
			display: block; } } }

/* button shutter close end */

/* shutter wide */


.shutter-wide {
	max-width: calc(100% - #{$header-width});

	@include media('<tab-lg') {
		max-width: 100%; }

	.shutter__align {
		width: 100%; }

	.shutter__holder {
		width: 100%; } }

/* shutter wide end */

/* shutter end */
