@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*search form */
.search-form {
	@include padding-vertical(50px); }

.search-form, .search-form__field {
	position: relative;
	margin-bottom: $bottom-space--md;

	&:last-child {
		margin-bottom: 0; } }

.search-form .light-form {
	.label-holder {
		@include font-bold;
		@include font(48px, 52px);

		&::before {
			border-top-width: 2px; } }

	label {
		height: 76px;
		@include padding-vertical(12px); } }

input.search-form__input {
	&, .light-form & {
		padding: 12px #{$search-form-popup-btn-size+12} 12px 0;
		@include font-bold;
		@include font(48px, 52px);
		height: 76px; } }

//  
.search-form__btn {
	position: absolute;
	right: 0;
	top: 50%;
	@include size($search-form-popup-btn-size);
	margin-top: -#{round($search-form-popup-btn-size*0.5)};
	@include font-normal;
	@include clear-spacing;
	text-align: center;
	z-index: 5;
	transition-property: color, fill, opacity;
	transition-duration: $animation-speed;
	//    svg 
	color: $text-color;
	fill: $text-color;
	pointer-events: none;
	opacity: 0.2;

	.input--has-value & {
		pointer-events: auto;
		opacity: 1; }

	.no-touchevents &:hover {
		color: $base-color;
		fill: $base-color; }

	// ea
	.no-touchevents .ea-site &:hover {
		color: $base-color--ea;
		fill: $base-color--ea; }

	input {
		@include cover-abs();
		padding: 0;
		border: 0;
		opacity: 0;
		z-index: 99; }

	svg {
		display: inline-block;
		position: absolute;
		@include position(50%);
		@include size($search-form-popup-btn-size);
		margin: -#{round($search-form-popup-btn-size*0.5)} 0 0 -#{round($search-form-popup-btn-size*0.5)}; }

	path {
		fill: inherit;
		transition: inherit; }

	span {
		@include hide-text; } }

/*search form end */
