@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*scroll sections */
$main-section-max-width: 960px !default;
$main-section-max-width-lg: 960px !default;

.main-sections {
	.touchevents & {
		height: auto !important;
		transform: none !important;
		transition-duration: 0s !important;

		@include media('<tab-lg') {
			height: auto !important;
			transform: none !important;
			transition-duration: 0s !important; } } }

.main-section {
	position: relative;
	//background-color: #f3f3f3
	min-height: 100vh;
	overflow: hidden;

	@include media('<tab-lg') {
		height: auto !important;
		min-height: 0; }

	.fp-bg {
		background: #f3f3f3 no-repeat center;
		background-size: cover;
		position: absolute;
		left: -5px;
		top: -5px;
		@include size(calc(100% + 10px));
		//z-index: -1
		backface-visibility: hidden;
		transition-property: transform;
		//transform: translate(0, -33%)
		transition-duration: $animation-speed-section;
		z-index: -1;

		@include media('<tab-lg') {
			transform: none !important; }

		&::before {
			@include pseudo();
			@include position(0, 0);
			@include size(100%);
			background-color: rgba(0, 0, 0, 0.3);
			//+linear-gradient(to top, rgba(0, 0, 0, 0.81) 0, rgba(0, 0, 0, 0.7) 25%, rgba(0, 0, 0, 0.5) 50%, rgba(0, 0, 0, 0.4) 100%)

			//+media('<tab-sm')
			//include linear-gradient(to top, rgba(0, 0, 0, 0.81) 0, rgba(0, 0, 0, 0.7) 25%, rgba(0, 0, 0, 0.5) 50%, rgba(0, 0, 0, 0.4) 100%)

			//no-cssgradients &
 } }			//background: rgba(0, 0, 0, 0.5)

	.fp-no-animate & {
		.fp-bg {
			transition-duration: 0s; } }

	&.active, &.fp-completely {
		.fp-bg {
			transition-duration: $animation-speed-section; } }

	//&.active
	//fp-bg
	//transform: translate(0, 0)

	&.fp-prev-before .fp-bg {
		//transform: translate(0, 0)
		transform: translate(0, 33%);
 }		//transition-duration: $animation-speed-section

	.fp-up &.fp-prev-before .fp-bg {
		//transform: translate(0, 0)
		transform: translate(0, -33%); }

	//&.fp-prev .fp-bg
	//ransform: translate(0, 0)
 }	//ransition-duration: 0s

.main-section__container {
	position: relative;

	@include media('>=tab-lg') {
		display: table;
		width: 100%;
		height: 100%;
		min-height: 100vh; } }

.main-section__align {
	@include media('>=tab-lg') {
		display: table-cell;
		vertical-align: middle;
		text-align: center; } }

.main-section__layout {
	position: relative;
	@include push-auto;
	max-width: $main-section-max-width;
	text-align: left;

	.main-section__align & {
		display: inline-block;
		width: 100%; }

	@include media('<desk-lg') {
		max-width: $main-section-max-width-lg; } }

.main-section__title {
	display: flex;
	justify-content: space-between;
	@include font-bold;
	@include font(48px, round-float(58/48, 1));
	margin-bottom: 10px;
	color: white;

	@include media('<desk-hd') {
		font-size: 42px;
		margin-bottom: 20px; }

	@include media('<desk-hd') {
		font-size: 38px;
		margin-bottom: 10px; }

	@include media('<tab-lg') {
		display: block;
		text-align: center;
		margin-bottom: 0.6em; }

	@include media('<tab-sm') {
		font-size: 30px; }

	h2, h3 {
		margin: 0;
		font-family: inherit;
		@include font(inherit, inherit);
		font-weight: inherit;
		color: inherit;
		flex: 1 1 12rem;

		@include media('<tab-lg') {
			margin-bottom: 0.2em;

			&:last-child {
				margin-bottom: 0; } } }

	.subtitle {
		@include font-normal;
		@include font(24px, round-float(30/24, 1));
		flex: 1 1 12em;
		padding: 0.5em;

		@include media('<desk-hd') {
			font-size: 20px; }

		@include media('<desk-sb') {
			font-size: 16px;
			padding: 0; }

		@include media('<tab-lg') {
			font-size: 18px; } } }

.main-section__holder {
	@include clear;
	margin-bottom: $bottom-space--md;

	&:last-child {
		margin-bottom: 0; } }

/*scroll sections end */

/*main section entry */
.main-section--entry {
	color: white;

	.fp-bg {
		background-image: url(../../img/bg-entry.jpg);

		@include media('<tab-lg') {
			display: none; } }

	.main-section__align {
		@include padding-vertical(120px); }

	.main-section__container {
		@include media('<tab-lg') {
			background: #f3f3f3 url(../../img/bg-entry.jpg) no-repeat center;
			background-size: cover;

			&::before {
				@include pseudo();
				@include position(0, 0);
				@include size(100%);
				//background-color: rgba(0, 0, 0, 0.4)
				@include linear-gradient(to top, rgba(0, 0, 0, 1) 0, rgba(0, 0, 0, 0.6) 25%, rgba(0, 0, 0, 0.4) 50%, rgba(0, 0, 0, 0.3) 100%); } } }

	.main-section__title {
		position: relative;
		padding-bottom: 0.8em;
		margin-bottom: 100px;
		@include font-bold;
		@include font(52px, 1.2);
		text-transform: uppercase;
		color: white;

		@include media('<desk-hd') {
			font-size: 48px; }

		@include media('<desk-sb') {
			font-size: 36px;
			margin-bottom: 80px; }

		@include media('<tab-lg') {
			margin-bottom: 20px;
			padding-bottom: 0;
			text-align: center;

			&:last-child {
				margin-bottom: 0; } }

		@include media('<tab-sm') {
			@include font(22px, round-float(30/22, 1));
			letter-spacing: 0.05em; }

		&::before {
			@include pseudo();
			left: 0;
			bottom: 0;
			@include size(85px, 3px);
			background-color: white;

			@include media('<tab-lg') {
				content: none; } }

		h1, h2, h3 {
			width: 800px;
			max-width: 100%;

			@include media('<desk-sb') {
				width: auto;
				max-width: none; } } }

	.main-section__footer {
		text-align: center; } }

/*animation show on scroll end */
/*main section entry end */

/*total date */
.total-data {
	@include media('>=tab-lg') {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		z-index: 50;

		&:last-child {
			margin-bottom: 0; } }

	@include media('<tab-lg') {
		background-color: black; }

	@include media('<tab-sm') {
		display: none; } }

.total-data__list {
	position: relative;
	@include margin-horizontal(-20px);
	@include clear;

	@include media('<desk-sm') {
		@include margin-horizontal(-10px); }

	&::before {
		@include pseudo();
		left: 0;
		top: 0;
		width: 108px;
		height: 114px;
		background: url(../../img/bg-triangle.png) no-repeat;
		background-size: cover;
		z-index: -1;
		transform: translate(-30px, -20px);

		@include media('<desk-lg') {
			content: none; } } }

.total-data__item {
	float: left;
	width: 33.3%;
	padding: 30px 20px 10px 20px;

	@include media('<desk-sm') {
		padding: 20px 10px 10px 10px; }

	@include media('<tab-sm') {
		float: none;
		width: auto;
		text-align: center; } }

.total-data__title {
	margin-bottom: 0.05em;
	@include font-light;
	@include font(90px, 1);
	color: $base-color;
	white-space: nowrap;

	@include media('<desk-hd') {
		font-size: 78px; }

	@include media('<desk-sb') {
		font-size: 64px; }

	@include media('<desk-sm') {
		font-size: 42px;
		margin-bottom: 10px; }

	.prefix, .unit {
		@include font-normal; }

	.prefix {
		position: relative;
		@include display-inline-block-top;
		@include margin($top: 0.1em, $right: 1em);
		@include font(24px, 1.3);
		color: white;

		@include media('<desk-sm') {
			font-size: 18px;
			margin-right: 0.5em; } }

	.unit {
		position: relative;
		display: inline-block;
		vertical-align: bottom;
		@include margin($bottom: 0.1em, $left: 1em);
		@include font(34px, 1.3);
		color: white;

		@include media('<desk-sm') {
			font-size: 22px;
			margin-left: 0.5em; } } }

.total-data__text {
	@include font(1.6rem, 1.2); }

/*total date end */

.fp-enabled {
	.main-section--entry {
		@include animation-on-scroll(main-section__title total-data); } }

/*main section news */
.main-section--news {
	background-size: cover;

	.fp-bg {
		background-color: #fff;
		background-image: url('../../img/bg-news.jpg'); } }

/*main section news end */

.fp-enabled {
	.main-section--news {
		@include animation-on-scroll(main-section__title news-grid main-section__footer); } }

/*main section providers */
.main-section--providers {
	background-size: cover;

	.fp-bg {
		background-color: #4f4d47;
		background-image: url('../../img/bg-providers.jpg');

		&::before {
			background-color: rgba(0, 0, 0, 0.6); } } }

/*main section news end */

.fp-enabled {
	.main-section--providers {
		@include animation-on-scroll(main-section__title agency providers plea); } }

/*main section map */
.main-section--map {
	@include font-normal;
	@include font(1.8rem, 1.222);
	color: #092140;
	cursor: default;

	.main-section__title {
		position: relative;
		color: $text-color;
		margin-bottom: 0.5em;
		padding-bottom: 0.5em;

		&::before {
			@include pseudo();
			bottom: 0;
			left: 0;
			@include size(64px, 3px);
			background-color: $text-color;

			@include media('<tab-lg') {
				left: 50%;
				margin-left: -(round(64px/2)); } } }

	.main-section__layout {
		@include media('>=tab-lg') {
			height: 0; } }

	.main-section__pos {
		@include media('>=tab-lg') {
			position: absolute;
			left: 0;
			top: 0;
			max-width: 100%;
			transform: translate(0, -50%);
			z-index: 40;
			@include antialiased; }

		@include media('<tab-lg') {
			background-color: white;
			padding-bottom: 70px; }

		@include media('<tab-sm') {
			padding-bottom: 40px; } }

	.phone {
		margin-bottom: 36px;
		@include font-bold;
		@include font(72px, round-float(86/72, 1));
		color: $text-color;

		@include media('<desk-hd') {
			margin-bottom: 20px;
			font-size: 64px; }

		@include media('<desk-sm') {
			font-size: 48px; }

		@include media('<tab-lg') {
			text-align: center; }

		@include media('<tab-sm') {
			font-size: 28px; }

		&:last-child {
			margin-bottom: 0; } }

	.info {
		margin-bottom: 3em;

		&:last-child {
			margin-bottom: 0; }

		@include media('<tab-lg') {
			text-align: center; } }

	.contacts {
		margin-bottom: 1.5em;
		max-width: 300px;
		@include font(1.6rem, (round-float(24/16, 2)));

		&:last-child {
			margin-bottom: 0; }

		@include media('<tab-lg') {
			@include push-auto; }

		li {
			padding-left: 0;
			margin: 0;

			&::before {
				content: none; } }

		a {
			color: inherit;
			text-decoration: none;
			transition-property: color;
			transition-duration: $animation-speed;

			.no-touchevents &:hover {
				color: $base-color; } } }

	//.main-section__holder > *
	//pacity: 0
	//isibility: hidden
	//ransform: translate(0, 20px)
	//ransition-property: transform, opacity, visibility, background-color, color, border-color
 }	//ransition-duration: $animation-speed-element

//.main-section--map.active .main-section__holder > *
//pacity: 1
//isibility: visible
//ransform: translate(0, 0)

//for $child from 0 through 4
//&:nth-child(#{$child})
//	transition-delay: (#{$animation-speed-element-delay + $child*0.3}, #{$animation-speed-element-delay + $child*0.3}, #{$animation-speed-element-delay + $child*0.3}, 0s, 0s, 0s)

/*main section map end */

/*main map */
.main-section .main-map {
	@include cover-abs();
	background-color: #d3d3d3;
	z-index: auto;

	@include media('<tab-lg') {
		position: relative;
		left: auto;
		top: auto;
		width: 100%;
		height: 320px; } }

/*main map end */

.fp-enabled {
	.main-section--map {
		@include animation-on-scroll(main-section__title phone info main-section__footer); } }

.c-categories {
	display: none;
	background-color: $base-color;

	.ea-site & {
		background-color: $base-color--ea; }

	@include media('<tab-lg') {
		display: block; } }

.c-categories-item {
	$icon-size: 50px !default;

	display: block;
	padding: #{round($text-layout-space-horizontal--md*0.6)} #{$text-layout-space-horizontal--md};
	text-decoration: none;
	color: $text-color;
	border: solid $side-menu-bg-color-hover;
	border-width: 1px 0 0;

	@include media('<tab-sm') {
		padding: #{round($text-layout-space-horizontal--sm*0.6)} #{$text-layout-space-horizontal--sm}; }

	&:first-child {
		border-top-width: 0; }

	svg {
		@include display-inline-block-middle;
		@include size($icon-size);
		margin-right: $text-layout-space-horizontal--md;

		@include media('<tab-sm') {
			margin-right: $text-layout-space-horizontal--sm; } }

	.c-categories__title {
		@include font-normal;
		@include font(18px, round-float(21/18, 2));
		text-transform: uppercase; }

	svg + .c-categories__title {
		@include display-inline-block-middle;
		width: calc(100% - #{$icon-size} - #{$text-layout-space-horizontal--md} - 6px); } }
