@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*agency */
.agency {
	counter-reset: section;
	text-align: center;
	margin-bottom: 30px;

	@include media('<desk-hd') {
		margin-bottom: 20px; }

	&:last-child {
		margin-bottom: 0; } }

.agency__holder {
	@include display-inline-block-top;
	@include clear-spacing;

	@include media('<tab-lg') {
		display: block; } }

.agency__item {
	position: relative;
	@include display-inline-block-middle;
	width: 33.3%;
	padding: 50px;
	counter-increment: section;
	@include font($font-size, $line-height);
	letter-spacing: normal;

	@include media('<desk-hd') {
		padding: 20px 50px; }

	@include media('<desk-sb') {
		padding: 5px 30px; }

	@include media('<tab-lg') {
		display: block;
		width: auto;
		max-width: 320px;
		@include push-auto;
		padding: 40px 0; }

	&::before {
		@include pseudo($content: counter(section));
		@include centering-transform();
		@include font-black;
		@include font(224px, 1);
		color: rgba(243, 243, 243, 0.35);

		@include media('<desk-hd') {
			font-size: 140px; }

		@include media('<desk-sb') {
			font-size: 80px; }

		@include media('<tab-lg') {
			font-size: 120px; } }

	&::after {
		@include pseudo();
		left: 0;
		top: 50%;
		width: 50px;
		height: 2px;
		margin-top: -1px;
		margin-left: -25px;
		@include linear-gradient(to right, #999 0, #999 33%, transparent 33%, transparent 100%);
		background-size: 6px 6px;

		@include media('<tab-lg') {
			top: 0;
			left: 50%; } }

	&:first-child::after {
		content: none; } }

.agency__inner {
	@include media('<tab-lg') {
		&::before {
			content: "";
			@include display-inline-block-middle;
			height: 60px; } } }

.agency__text {
	@include font-bold;
	@include font(18px, round-float(22/18, 1));
	color: white;
	text-transform: uppercase;

	@include media('<desk-hd') {
		font-size: 15px;
		letter-spacing: 0.025em; }

	@include media('<desk-sb') {
		font-size: 13px;
		letter-spacing: 0.05em; }

	@include media('<tab-lg') {
		font-size: 18px;
		@include display-inline-block-middle;
		width: calc(100% - 6px); } }

/*agency end */

/*providers */
.providers {
	margin-bottom: 30px;
	@include clear;

	@include media('<desk-hd') {
		margin-bottom: 20px; }

	&:last-child {
		margin-bottom: 0; } }

.providers__item {
	float: left;
	width: 50%;
	padding: 0 3.7%;

	@include media('<tab-sm') {
		float: none;
		width: auto;
		padding: 20px 0 0; }

	&:first-child {
		padding-left: 0;

		@include media('<tab-sm') {
			padding-top: 0; } }

	&:last-child {
		padding-right: 0; } }

.providers__text {
	background: black;
	padding: 27px 30px;
	@include font(15px, round-float(19/15, 1));
	color: white;

	@include media('<desk-hd') {
		padding: 20px 25px; }

	@include media('<desk-sb') {
		padding: 15px 20px;
		@include font(12px, 1.7); }

	@include media('<tab-sm') {
		font-size: 16px; }

	& > p, & > ul, & > ol {
		&:last-child {
			margin-bottom: 0; } } }

.providers__title {
	@include font-bold;
	@include font(2.0rem, round-float(24/20, 1));
	color: white;
	margin-bottom: 1em;

	@include media('<tab-sm') {
		text-align: center; }

	&:last-child {
		margin-bottom: 0; } }

.providers__list {

	&, .#{$user-content} & {
		margin: 0;

		li {
			padding-left: 48px;

			&::before {
				@include pseudo();
				left: 0;
				top: 10px;
				border: none;
				@include size(30px, 3px);
				background-color: $base-color; }

			@include media('<desk-sb') {
				@include margin-vertical(0.5em); } } }

	& {
		@include font(16px, round-float(24/16, 1));
		text-transform: uppercase;
		color: white;

		@include media('<desk-sb') {
			font-size: 14px; }

		a {
			color: $base-color; } } }

/*providers end */

/*plea */
.plea {
	width: 560px;
	max-width: 100%;
	@include push-auto;
	text-align: center;
	@include font(24px, round-float(36/24, 1));
	color: white;

	@include media('<desk-hd') {
		@include font(18px, round-float(24/18, 1)); }

	a {
		text-decoration: none;
		color: $base-color;

		span {
			border-bottom: 1px solid transparent;
			transition: border-color $animation-speed; }

		.no-touchevents & {
			text-decoration: none;

			span {
				border-bottom-color: $base-color; } } } }

/*plea end */
