@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* v-middle */
.v-middle {
 }	// overflow: hidden //    ,   overflow: hidden   

.v-middle__align {
	// width: calc(100% + 100px)
	overflow-x: hidden;
	overflow-y: auto;
	height: 100%; }

.v-middle__holder {
	// display: table
	// min-height: 100vh //    
	//      height
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	min-height: 100vh; // ie  min-height  table

	//     display: flex
	.no-flexbox & {
		display: table;
		table-layout: fixed;
		width: 100%; }

	//   11   flex-direction: column   min-height
	//     
	@media all and (-ms-high-contrast: none) {
		&::-ms-backdrop, & {
			display: table;
			table-layout: fixed;
			width: 100%; } } }

.v-middle__row {

	//     display: flex
	.no-flexbox & {
		display: table-row; }

	//   11   flex-direction: column   min-height
	//     
	@media all and (-ms-high-contrast: none) {
		&::-ms-backdrop, & {
			display: table-row; } } }

.v-middle__cell {
	//     display: flex
	.no-flexbox & {
		display: table-cell;
		vertical-align: middle; }

	//   11   flex-direction: column   min-height
	//     
	@media all and (-ms-high-contrast: none) {
		&::-ms-backdrop, & {
			display: table-cell;
			vertical-align: middle; } } }

/* v-middle end */
