@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* map site */
.map-site-title {
	display: none; }

.map-site__list {
	@include clear;

	.layout-wide & {
		max-width: none; }

	& > li {
		float: left;
		width: calc(33.3% - 40px);
		margin-left: 40px;

		&:nth-child(3n + 1) {
			clear: left; }

		@include media('<desk-hd') {
			width: calc(49.98%);

			&:nth-child(3n + 1) {
				clear: none; }

			&:nth-child(2n + 1) {
				clear: left; } }

		@include media('<tab-sm') {
			width: calc(100%);

			&:nth-child(2n + 1) {
				clear: none; } } } }

//       
.map-site__list > li {
	@include font-bold;
	font-size: 18px;
	text-transform: uppercase; }

//     
.map-site__drop {

	//       
	li {
		@include font-bold;
		font-size: 16px;
		text-transform: none; }

	.map-site__drop {
		//       
		li {
			font-size: 16px;
			@include font-normal;
			text-transform: none; }

		.map-site__drop {
			//       
			li {
				font-size: 14px; }

			a {
				color: #999; } } } }

/* map site end */
