@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
	font-size: 62.5%; }

body {
	@include font-normal;
	@include font($font-size, $line-height);
	color: $text-color;
	height: 100%;
	background: $bg-color url(../../img/empty.png) no-repeat fixed;
	// min-width: 1600px
	min-width: 320px;
	overflow-x: hidden;

	@include media('<tab-sm') {
		@include font($font-size--sm, $line-height--sm); } }

h1, h2, h3, h4, h5, h6 {
	padding: 0;
	@include font-alt-bold;
	color: $title-color;
	margin: 0 0 $bottom-space;

	@include media('<mob-lg') {
		margin: 0 0 round-float($bottom-space*0.85, 1); } }

h1 {
	@include font($font-size-h1, $line-height-h1);

	@include media('<tab-lg') {
		@include font($font-size-h1--md, $line-height-h1--md); }

	@include media('<mob-lg') {
		@include font($font-size-h1--sm, $line-height-h1--sm); } }

h2, .text-popup-title {
	@include font($font-size-h2, $line-height-h2);

	@include media('<tab-lg') {
		@include font($font-size-h2--md, $line-height-h2--md); }

	@include media('<mob-lg') {
		@include font($font-size-h2--sm, $line-height-h2--sm); } }

h3 {
	@include font($font-size-h3, $line-height-h3);

	@include media('<mob-lg') {
		@include font($font-size-h3--sm, $line-height-h3--sm); } }

h4 {
	@include font($font-size-h4, $line-height-h4); }

h5 {
	@include font($font-size-h5, $line-height-h5); }

h6 {
	@include font($font-size-h6, $line-height-h6);
	text-transform: uppercase; }

p {
	padding: 0;
	margin: 0 0 $bottom-space; }

hr {
	line-height: 1px;
	height: 0;
	border: none;
	background: none;
	border-bottom: 1px solid $border-color;
	margin-bottom: $bottom-space; }

a {
	color: $link-color;
	text-decoration: underline;
	cursor: default;
	outline: none;

	&:link {
		cursor: pointer; }

	.no-touchevents &:hover {
		text-decoration: none; }

	&[href^="tel"], .no-touchevents &[href^="tel"]:hover {
		text-decoration: none !important;
		color: inherit;
		cursor: default; }

	&[href^="javascript:"] {
		cursor: default; } }

img {
	border: none;
	vertical-align: top;
	max-width: 100%;

	table & {
		max-width: none; } }

b, strong, th {
	@include font-bold; }

i, em {
	@include font-italic; }

i b, i strong, em b, em strong, b i, b em, strong i, strong em {
	@include font-bold-italic; }

// user spacer
.user-spacer {
	height: $bottom-space--lg; }

// user spacer end

.entry-text {
	@include font-bold;
	font-size: 1.2em;
	margin-bottom: $bottom-space;

	//&:first-letter
	//loat: left
	//ont-size: 4em
	//ine-height: 1
	//include margin($top: 0.1em, $right: 0.3em)

	b, strong {
		@include font-bold; }

	i, em {
		@include font-bold-italic; } }

.entry-img {
	position: relative;
	overflow: hidden;
	@include ratio(144, 81);
	margin-bottom: $bottom-space--md;

	img {
		position: absolute;
		left: 50%;
		top: 50%;
		max-width: 100%;
		max-height: none;
		min-width: 0;
		min-height: 0;
		text-align: left;
		width: calc(100% + 4px);
		height: calc(100% + 4px);
		transform: translate(-50%, -50%);
		object-fit: cover;

		.no-objectfit & {
			width: auto;
			height: 100%; } } }

.entry-img__title {
	@include centering-transform-abs();
	margin: 0;
	font-size: $font-size-h1;
	@include font($font-size-h1, $line-height-h1);
	color: white;
	text-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
	text-align: center;
	z-index: 10;

	@include media('<desk-sm') {
		@include font($font-size-h1--sm, $line-height-h1--sm); } }

.mark {
	color: $mark-color;
	font-style: inherit;
	font-weight: inherit; }

.text-color-alt {
	color: $text-color-alt; }

.align-center {
	text-align: center; }

.align-left {
	text-align: left; }

.align-right {
	text-align: right; }

.float-left {
	float: left; }

.float-right {
	float: right; }

.nobr {
	white-space: nowrap; }

.heading {

	h1, h2, h3 {
		@include display-inline-block-middle;
		margin-right: 20px;
		margin-bottom: $bottom-space;

		&:last-child {
			margin-right: 0;
			margin-bottom: 0; }

		~ * {
			margin-bottom: $bottom-space !important; } } }

.heading-user__caption {
	@include display-inline-block-middle;
	@include clear-spacing;
	margin-right: 20px;
	margin-bottom: $bottom-space;

	@include media('<mob-lg') {
		display: block;
		margin-right: 0;
		text-align: center; }

	&:last-child {
		margin-right: 0;
		margin-bottom: 0; }

	~ * {
		margin-bottom: $bottom-space !important; } }

.heading-user__ava {
	@include display-inline-block-middle;
	overflow: hidden;
	background-color: $bg-color-light;
	@include size(70px);
	@include font($font-size, $line-height);
	letter-spacing: normal;
	border-radius: 70px;
	margin-right: 20px;

	@include media('<mob-lg') {
		display: block;
		@include push-auto; }

	img {
		@include size(70px);
		border-radius: 70px; } }

.heading-user__title {
	@include display-inline-block-middle;
	@include font-bold;
	@include font($font-size-h3, $line-height-h3);
	letter-spacing: normal;

	@include media('<mob-lg') {
		display: block; } }

.heading-user__name {
	@include font-normal;
	font-size: 1.8rem;
	white-space: nowrap; }

.heading, .heading-user {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
	margin-bottom: $bottom-space;

	//+media('<mob-lg')
	//argin-bottom: 0

	.add-info {
		@include display-inline-block-middle;
		margin-bottom: $bottom-space; } }

.heading-user {
	@include media('<mob-lg') {
		display: block;
		text-align: center; } }

/*content img*/
.imgc {
	text-align: center;
	margin-bottom: $bottom-space;
	clear: both;

	&.layout-full-width {
		margin-bottom: $bottom-space--md; } }

.imgl {
	text-align: left;
	float: left;
	margin: 0 $bottom-space $bottom-space 0; }

.imgr {
	text-align: right;
	float: right;
	margin: 0 0 $bottom-space $bottom-space; }

.imgl, .imgr {
	max-width: 50%;

	@include media('<tab-sm') {
		float: none;
		display: block;
		margin-left: auto;
		margin-right: auto;
		max-width: none; } }

/*content img end*/

/*columnize*/
.columnize, .columnize--3, .columnize--4 {
	column-count: 2;
	column-gap: 2em;
	margin-bottom: $bottom-space;
 }	//font-size: 0.9em

.columnize--3 {
	column-count: 3;
 }	//font-size: 0.85em

.columnize--4 {
	column-count: 4;
	//font-size: 0.8em

	@include media('<desk-sm') {
		column-count: 3; } }

.columnize--3, .columnize--4 {

	@include media('<tab-lg') {
		column-count: 2; } }

.columnize, .columnize--3, .columnize--4 {

	@include media('<tab-sm') {
		column-count: 1; } }

/*columnize end*/

.#{$user-content} {
	@each $tags in h2, h3, h4, h5 {
		p + #{$tags}, ul + #{$tags}, ol + #{$tags} {
			margin-top: round-float($bottom-space*1.5, 1); } } }

/*list*/
.#{$user-content} {
	//list
	ul, ol {
		margin: $bottom-space 0;
		padding: 0;
		list-style: none; }

	ul li, ol li {
		padding-left: 50px;
		margin: round($bottom-space*0.5) 0;
		position: relative; }

	ul > li::before {
		@include pseudo();
		left: 0;
		top: 14px;
		@include size(30px, 0);
		border: solid $outline-color;
		border-width: 3px 0 0;
		box-sizing: border-box; }

	ol {
		counter-reset: section;

		& > li {
			counter-increment: section;

			&::before {
				content: counter(section);
				position: relative;
				@include display-inline-block-top;
				@include size(26px);
				margin: 2px 24px -2px -50px;
				@include font-normal;
				@include font(13px, 24px);
				color: $text-color;
				text-align: center;
				border: 1px solid $outline-color;
				border-radius: 50%; } } }

	ul ul, ul ol, ol ul {
		margin: round($bottom-space*0.5) 0; } }

/*list reset */
.reset-list, .#{$user-content} .reset-list {
	list-style: none;

	li, li:first-child {
		padding: 0;

		&::before {
			content: none; } } }

/*list reset end */
/*list end*/

/*table*/
.table-auto {
	position: relative;
	overflow: auto;
	max-width: 100%;
	-webkit-overflow-scrolling: touch;
	margin-bottom: $bottom-space--md;

	table {
		margin-bottom: 0;
		width: auto;
		min-width: 100%; } }
table {
	border-collapse: collapse;
	border-spacing: 0;
	width: auto;

	tr {
		@include clear-spacing; }

	td, th {
		@include font($font-size--sm, $line-height--sm);
		letter-spacing: normal;
		color: $table-text-color;
		padding: 8px 14px;
		border-color: $table-border-color; } }

.#{$user-content} {
	table {
		margin-bottom: $bottom-space--md; } }

.table-auto table {
	margin-bottom: 0; }

.tbl {
	border: none;

	td, th {
		color: $table-text-color;
		background-color: $table-bg-color;
		border-style: solid;
		border-width: 0 0 1px;
		transition-property: background-color;
		transition-duration: $animation-speed; }

	tr {
		.no-touchevents &:hover td {
			background-color: $bg-color-light; } }

	th, thead td {
		@include font-bold;
		text-align: left;
		border-width: 0 0 2px; }

	th, thead td, tfoot td {
		background: $table-bg-alt-color;
		color: $table-text-alt-color;
 } }		//border-top-width: 0

//   ( 1)

.tbl-resp {
	$tbl-resp-border-color: #ddd;

	@include media('<tab-sm') {
		border: 1px solid $tbl-resp-border-color;

		[class*="tbl-resp--grid-col-"] {
			@include display-inline-block-middle;
			position: relative;
			min-height: 1px; }

		@for $grid from 1 through 12 {
			.tbl-resp--grid-col-#{$grid} {
				width: (100%/(12)*$grid); } }

		&, thead, tbody, tfoot, tr, td, th {
			display: block; }

		[class*="tbl-resp--grid-col-"] {
			@include display-inline-block-middle; }

		.tbl-resp--title, thead tr, .tbl-resp--head {
			background-color: #333;
			color: white; }

		.tbl-resp--title, .tbl-resp--subtitle {
			@include font-bold;
			@include font(1rem, 1.6);
			text-align: center;
			letter-spacing: 0.05em;
			text-transform: uppercase; }

		.tbl-resp--subtitle, tr {
			border-width: 0 0 1px; }

		.tbl-resp--hide {
			display: none; }

		td, th, tr {
			border: 0 solid $tbl-resp-border-color; }

		tr {
			border-width: 0 0 1px;

			&:last-child {
				border-bottom: none; } }

		td, th {
			border-width: 0 0 0 1px;
			text-align: center;
			min-width: 0;

			&:first-child {
				border-left: none; } }

		.tbl-resp--hide, .tbl-resp--subtitle {
			& + td, & + th {
				border-left: none; } }

		tr:last-child td:not(.tbl-resp--subtitle) {
			border-bottom: none; } } }

//   ( 1) end

//   ( 2)
.tbl-flex {
	@include media('<tab-sm') {
		&, thead, tbody, tfoot, tr, td, th {
			display: block; }

		thead {
			display: none; }

		.tbl-resp--title,
		.no-touchevents & tr:hover .tbl-resp--title {
			@include font-bold;
			@include font(1.2rem, 1.6);
			text-align: center;
			letter-spacing: 0.05em;
			text-transform: uppercase;
			background-color: #333 !important;
			color: white !important;
			border: none; }

		.tbl-flex--hide {
			display: none; }

		td, th {
			@include font(16px, 18px);
			border-width: 0 1px;
			text-align: right;
			min-width: 0;
			cursor: default;
			@include clear;

			&:nth-child(even) {
				background-color: $table-bg-alt-color; }

			&:first-child {
				border-left: none; }

			&::before {
				content: attr(data-label);
				float: left;
				vertical-align: middle;
				max-width: 50%;
				padding-right: 10px;
				font-size: 12px;
				text-transform: uppercase;
				text-align: left; } }

		.no-touchevents & tr:hover td {
			background-color: $table-bg-color; }

		.no-touchevents & tr:hover td:hover {
			background-color: $table-bg-alt-color; }

		tr:last-child td:last-child {
			border-bottom-width: 1px; } } }



//   ( 2) end

/*table end*/

/*blockquote*/
.#{$user-content} {

	blockquote {
		display: block;
		margin: $bottom-space--lg 0;
		padding: 40px 0;
		border: solid $outline-color;
		border-width: 2px 0;

		q {
			display: block;
			max-width: 720px;
			margin: 0 auto 20px;
			font-family: $font-alt;
			@include font(round-float(24px, 1), 36px);
			color: $text-color-alt;
			text-align: center;

			&::before,
			&::after {
				content: none; } }

		cite {
			display: block;
			margin-left: auto;
			@include font-normal;
			@include font($font-size, $line-height);
			color: $text-color-light;
			text-align: center; }

		.ea-site & {
			q {
				color: $base-color--ea; } } } }

/*blockquote end*/

/*datetime*/
.datetime {
	display: block;
	font-style: normal;
	margin-bottom: $bottom-space;
	color: $text-color-light; }

/*datetime end*/
