@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* TEXT POPUP */
.text-popup {
	position: fixed;
	right: 0;
	top: 0;
	height: 100%;
	width: $text-popup-width;
	background-color: $text-popup-bg-color;
	opacity: 0;
	visibility: hidden;
	z-index: 1011;
	transition-property: width;
	transition-duration: $animation-speed;

	@include media('<tab-lg') {
		width: 100%; }

	//+media('>=desk-md')
	//isibility: inherit !important
	//pacity: 1 !important
 }	//ransform: matrix(1, 0, 0, 1, 0, 0) !important

.text-popup-layout {
	overflow-x: hidden;
	overflow-y: auto;
	height: 100vh;
 }	//padding: 63px #{$text-layout-space-horizontal--lg} #{round($text-layout-space-horizontal--lg*0.6)}

.text-popup-heading {
	position: relative;
	@include clear;

	.btn-text-popup-close {
		float: right; }

	& > * {
		overflow: hidden; } }

.text-popup-title {
	margin-bottom: $bottom-space;
	@include font-bold;
	color: $text-color; }

/* forms */
.text-popup {
	.form-row {
		@include margin-horizontal(-20px);

		.input-wrap {
			@include margin-horizontal(20px);
			width: calc(100% * 0.5 - 40px);

			@include media('<mob-lg') {
				display: block;
				width: auto; } } } }

/* forms end */

/* check list */
.text-popup {
	.check-list {
		@include clear-spacing;

		&, .#{$user-content} & {
			@include grid-parent(20px);

			li {
				@include grid-item(3, 20px);
				letter-spacing: normal;

				@include media('<mob-lg') {
					@include grid-item-responsive(2, 20px); }

				@include media('<mob-md') {
					@include grid-item-responsive(1, 20px); } } } } }

/* check list end */
/* TEXT POPUP end */
