@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*scroll menu */
.scroll-nav {
	position: fixed;
	right: $sidebar-width + $text-layout-space-horizontal;
	top: $top-panel-height + $text-layout-space-horizontal;
	z-index: 5;

	.home-page & {
		right: $sidebar-width-alt + $text-layout-space-horizontal; }

	@include media('<desk-hd') {
		right: $sidebar-width + $text-layout-space-horizontal--lg;
		top: $top-panel-height + $text-layout-space-horizontal--lg;

		.home-page & {
			right: $sidebar-width-alt + $text-layout-space-horizontal--lg; } }

	@include media('<desk-lg') {
		display: none; }

	@include media('<tab-lg') {
		right: $sidebar-width + $text-layout-space-horizontal--md;
		top: $top-panel-height--md + $text-layout-space-horizontal--md;

		.home-page & {
			right: $sidebar-width-alt + $text-layout-space-horizontal--md; } }

	@include media('<tab-sm') {
		right: $sidebar-width + $text-layout-space-horizontal--sm;
		top: $top-panel-height--md + $text-layout-space-horizontal--sm;

		.home-page & {
			right: $sidebar-width-alt + $text-layout-space-horizontal--sm; } }

	&, .#{$user-content} & {
		margin: 0;

		li {
			margin: 0 0 10px;
			padding: 0;

			&:before {
				content: none; } } }
	& {
		a {
			display: block;
			position: relative;
			width: 40px;
			height: 40px;
			text-indent: -9999px;
			overflow: hidden;
			background-color: black;
			transition-property: background-color;
			transition-duration: $animation-speed;

			&::before {
				@include pseudo();
				left: 0;
				bottom: 0;
				height: 100%;
				width: 100%;
				background-color: $base-color;
				transition-property: transform;
				transition-duration: 0.3s;
				transform: translate(0, 100%); }

			svg {
				@include centering-transform-abs();
				@include size(22px); }

			path {
				fill: white; }

			.no-touchevents &:hover {
				&::before {
					transform: translate(0, calc(100% - 2px)); } } }

		li.active a {
			&, .no-touchevents & {
				//background-color: $base-color
				cursor: default;

				&::before {
					transform: translate(0, 0); } } } } }

/*scroll menu end */
