@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* depicts */
[class|="depict"], [class*=" depict-"] {
	@include display-inline-block-top;
	@include size(16px); }

.depict-cart {
	@include size(24px);
	background-image: url(../../img/depict-cart.svg);
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain; }

.depict-plus {
	$this-icon-lines-width: 10px;
	$this-icon-lines-height: 2px;
	$this-icon-lines-color: $text-color;

	position: relative;
	@include size($this-icon-lines-width);
	backface-visibility: hidden;

	&::before, &::after {
		@include pseudo();
		left: 0;
		top: 50%;
		@include size($this-icon-lines-width, $this-icon-lines-height);
		margin-top: -#{round($this-icon-lines-height/2)};
		backface-visibility: hidden;
		background-color: $this-icon-lines-color;
		transition-property: background-color;
		transition-duration: $animation-speed; }

	&::after {
		transform: rotate(0.25turn); } }

.depict-remove {
	$this-icon-lines-width: 14px;
	$this-icon-lines-height: 1px;
	$this-icon-lines-color: $text-color;

	position: relative;
	@include size(24px);
	background-color: transparent;
	backface-visibility: hidden;
	//transition-property: background-color
	//transition-duration: $animation-speed

	&::before, &::after {
		@include pseudo();
		left: 50%;
		top: 50%;
		@include size($this-icon-lines-width, $this-icon-lines-height);
		margin: -#{round($this-icon-lines-height/2)} 0 0 -#{round($this-icon-lines-width/2)};
		backface-visibility: hidden;
		background-color: white; }

	&::before {
		transform: rotate(45deg); }

	&::after {
		transform: rotate(-45deg); } }

/* depicts end */
