@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*buttons */
[class|="btn"], [class*=" btn-"], input[type="submit"], input[type="button"], input[type="reset"], button {
	@include display-inline-block-top;
	position: relative;
	text-align: center;
	text-decoration: none;
	cursor: pointer; }

[class|="btn"], [class*=" btn-"] {
	& input[type="submit"], & input[type="button"], & input[type="reset"] {
		background: url("../../img/empty.png") repeat;
		display: block;
		@include cover-abs();
		padding: 0;
		border: none;
		z-index: 99;
		opacity: 0; } }

//btn-default
.btn-default, .btn-default-alt {
	position: relative;
	@include font-alt-bold;
	@include font(16px, 24px);
	text-align: center;
	color: $text-color;
	padding: 12px 25px;
	border: 1px solid transparent;
	background: $base-color;
	box-shadow: none;
	transition-property: background-color, color, border-color;
	transition-duration: $animation-speed;
	margin: 0;

	.no-touchevents &:hover {
		background-color: tint($base-color, 70%); }

	//&:active
	//ransform: scale(0.95)

	// ea
	.ea-site & {
		background: $base-color--ea;

		.no-touchevents &:hover {
			background-color: tint($base-color--ea, 70%); } } }

//btn-default-alt
.btn-default-alt {
	&, .ea-site & {
		color: $text-color;
		border: 1px solid $text-color;
		background-color: transparent;

		.no-touchevents &:hover {
			background-color: $bg-color-light; } } }

// btn-more
.btn-main {
	position: relative;
	min-width: 240px;
	padding: 13px 30px;
	@include font(1.6px, 1.3);
	color: $text-color;
	background-color: transparent;
	border: 1px solid $text-color;
	transition-property: background-color, color, border-color;
	transition-duration: $animation-speed;

	.no-touchevents &:hover {
		background-color: $base-color;
		border-color: $base-color; }

	.no-touchevents .ea-site &:hover {
		background-color: $base-color--ea;
		border-color: $base-color--ea; }

	//&:active
	//ransform: translate(0, 2px) scale(0.95)

	& + span {
		display: inline-block;
		vertical-align: middle; }

	svg {
		display: inline-block;
		vertical-align: middle;
		@include size(20px);
		margin-right: 20px; }

	path {
		fill: $text-color; } }

/* btn-more */
.btn-more {
	display: block;
	position: relative;
	min-width: 240px;
	padding: 12px 29px 13px;
	@include font(16px, 24px);
	text-align: center;
	text-decoration: none;
	white-space: nowrap;
	color: $text-color;
	border: 1px solid $base-color;
	background-color: transparent;
	transition-property: background-color, color, border-color;
	transition-duration: $animation-speed;
	margin: 0;

	.ea-site & {
		border-color: $base-color--ea; }

	.no-touchevents &:hover {
		background-color: $base-color; }

	.no-touchevents .ea-site &:hover {
		background-color: $base-color--ea; } }

.btn-main--more {
	display: block;
	position: relative;
	min-width: 240px;
	padding: 12px 29px 13px;
	@include font(14px, 1.5);
	color: white;
	background-color: transparent;
	border: 1px solid $base-color;
	@include antialiased;
	transition-property: background-color, color;
	transition-duration: $animation-speed;

	& > span {
		position: relative;
		padding-right: 35px;
		filter: drop-shadow(1px 1px 0 rgba(0, 0, 0, 0.35));
		transition-property: filter;
		transition-duration: $animation-speed;

		&::before, &::after {
			@include pseudo();
			top: 50%;
			right: 0;
			width: 10px;
			height: 2px;
			background-color: white;
			backface-visibility: hidden;
			transition-property: background-color;
			transition-duration: $animation-speed; }

		&::before {
			transform: rotate(0.25turn); } }

	.no-touchevents &:hover {
		background-color: $base-color;
		color: $text-color;

		& > span {
			filter: drop-shadow(1px 1px 0 rgba(0, 0, 0, 0.0));

			&::before, &::after {
				background-color: $text-color; } } } }

/* btn-outline */
.btn-outline, .btn-outline-alt {
	position: relative;
	@include font-alt-bold;
	@include font(14px, 24px);
	text-align: center;
	text-decoration: none;
	text-transform: uppercase;
	letter-spacing: 0.05em;
	white-space: nowrap;
	color: $text-color;
	padding: 11px 24px 9px;
	border: 3px solid $base-color;
	background-color: transparent;
	transition-property: background-color, color, border-color;
	transition-duration: $animation-speed;
	margin: 0;

	.no-touchevents &:hover {
		background-color: $base-color; }

	.ea-site & {
		border-color: $base-color--ea; }

	.no-touchevents .ea-site &:hover {
		background-color: $base-color--ea; } }

//btn-default-alt
.btn-outline-alt {
	&, .ea-site & {
		@include font-normal;
		@include padding-vertical(12px, 10px);
		font-size: 12px;
		color: $text-color;
		border: 1px solid $text-color;
		background-color: transparent;

		& > span {
			letter-spacing: normal; }

		.no-touchevents &:hover {
			//background: $base-color
			//border-color: $base-color
			background-color: $bg-color-light; } } }

