/* button catalog menu toggle */

/*view print*/
.view-print {
	@include display-inline-block-top;
	padding: 6px 12px;
	margin-bottom: $bottom-space;
	line-height: 1;
	color: black;
	text-decoration: none;
	background: #f9f9f9;
	transition-property: background;
	transition-duration: $animation-speed;

	.no-touchevents &:hover {
		background-color: #eaeaea; }

	svg {
		@include display-inline-block-middle;
		@include size(20px);
		margin-right: 5px; }

	span {
		@include display-inline-block-middle; }

	@include media('<tab-lg') {
		font-size: 14px; }

	@include media('<mob-lg') {
		padding: 15px;

		& svg {
			margin-right: 0; }

		& span {
			display: none; } } }

/*view print end*/

/* button remove */
.btn-remove {
	background-color: #666;
	transition-property: background-color;
	transition-duration: $animation-speed;
	color: white;
	text-decoration: none;

	& > span {
		@include hide-text; }

	& > i {
		transition-property: background-color;
		transition-duration: $animation-speed; }

	.no-touchevents &:hover {
		background-color: tomato; } }


/* button remove end */
/*buttons end */
