@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* enter form */
.enter-form {
	@include padding-vertical(50px);
	max-width: 480px;
	@include push-auto;

	.form-footer {
		text-align: center; }

	.form-buttons {
		padding-top: 20px;
		margin-bottom: 10px;

		&:last-child {
			margin-bottom: 0; } }

	.btn-submit {
		min-width: 180px; }

	.form-note {
		@include font(#{$font-size--less}, #{$line-height--less}); } }

/* enter form end */
