@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

.logo-nav {
	display: block;
	padding: 15px #{$text-layout-space-horizontal--lg};
	text-decoration: none;
	color: $text-color;
	border-style: solid;
	border-color: inherit;
	border-width: 0 0 1px;
	background-color: $bg-color-light;
	transition-property: color, background-color;
	transition-duration: $animation-speed;

	@include media('<tab-lg') {
		@include padding-horizontal($text-layout-space-horizontal--md); }

	@include media('<tab-sm') {
		@include padding-horizontal($text-layout-space-horizontal--sm); }

	@include media('>=desk-sm') {
		.no-touchevents &:hover {
			background-color: darken($bg-color-light, 10%); } }

	//  
	//.home-page &
	//olor: white
	//ackground-color: $logo-bg-color

	//+media('>=desk-sm')
	//no-touchevents .home-page &:hover
	//background-color: lighten($logo-bg-color, 10%)

	//   end

	@include media('<tab-lg') {
		@include display-inline-block-top;
		padding: 0;
		border: none;
		//background-color: transparent
 } }		//border-color: #ccc

.logo-nav__img {
	@include display-inline-block-middle;
	@include size(152px, auto);
	background-image: url(../../img/logo.png);
	background-size: 100%;
	background-repeat: no-repeat;
	margin-bottom: 10px;

	&::before {
		@include pseudo-block-relative();
		@include ratio(152, 47); }

	& > span {
		@include hide-text; }

	//.home-page &
	//ackground-position: 0 100%

	.ea-site & {
		@include size(178px, auto);
		background-image: url(../../img/logo--ea.png);

		&::before {
			@include ratio(178, 42); }

		@include media('<tab-lg') {
			width: 144px; } } }

.logo-nav__title {
	display: block;
	@include font(10px, 14px);
	letter-spacing: 0.05em;
	text-transform: uppercase; }

.popup-overlay--nav {
	@include media('<tab-lg') {
		top: #{$top-panel-height--md} !important; } }

.nav {
	position: relative;
	@include padding-vertical(20px); }

/* nav list */
//      
.nav__list {
	position: relative;

	@include media('<tab-lg') {
		width: auto; } }

//     
.nav__drop {
	position: relative;
	//background-color: $nav-bg-color
	display: none;

	.no-js & {
		display: block; } }

//     
.nav__list {

	li {
		position: relative;
		overflow: hidden; }

	.nav__tab {
		position: relative; }

	a {
		display: block;
		position: relative;
		padding: 0 #{$text-layout-space-horizontal--lg};
		color: $nav-text-color;
		text-decoration: none;
		transition-property: color, background-color;
		transition-duration: $animation-speed;
		@include clear-spacing;

		@include media('<tab-lg') {
			@include padding-horizontal($text-layout-space-horizontal--md); }

		@include media('<tab-sm') {
			@include padding-horizontal($text-layout-space-horizontal--sm); }

		&::before {
			@include pseudo-inline-block-relative();
			vertical-align: middle;
			min-height: 36px; }

		span {
			@include display-inline-block-middle;
			max-width: calc(100% - 5px);
			@include font(16px, 20px);
			letter-spacing: normal;
			padding: 8px 0; }

		.no-touchevents &:hover {
			color: $base-color; }

		.no-touchevents .ea-site &:hover {
			color: $base-color--ea; } } }

//    ,    
.nav__list li.has-drop > .nav__tab > a {
	padding-right: (round($nav-arrow-size+10)); }

//      
.nav__list li.current > .nav__tab {

	&, .no-touchevents & {

		a, a:hover {
			color: $base-color; } }

	.ea-site &, .ea-site .no-touchevents & {

		a, a:hover {
			color: $base-color--ea; } } }

//       
.nav__list li {
	&.is-open {
		& > .nav__tab a {
			&::before {
				opacity: 1;
				transform: translate(0, 0); } }

		//& > .nav__drop
		//isplay: block

 } }		//    nav__drop style="display: block"

//       
.nav__list > li {

	& > .nav__tab a {
		@include font-bold;
		text-transform: uppercase;
		min-height: 0;

		span {
			letter-spacing: 0.05em; } } }

//  
.nav__angle {
	position: absolute;
	right: 0;
	top: 0;
	width: $nav-arrow-size;
	height: 100%;
	//border: solid #666
	//border-width: 0 0 0 1px
	transition-property: transform, color, background-color;
	transition-duration: $animation-speed;
	z-index: 2;
	cursor: pointer;
	display: none;

	li.has-drop > .nav__tab > & {
		display: block; }

	i {
		position: absolute;
		@include position(50%, 50%);
		@include size(16px);
		margin: -8px 0 0 -8px;
		pointer-events: none;
		transition-property: transform;
		transition-duration: $animation-speed;
		@include font(0, 0);

		&::before, &::after {
			@include pseudo();
			@include position(50%, 0);
			height: 2px;
			width: 16px;
			backface-visibility: hidden;
			background-color: $nav-text-color;
			transition-property: background-color, transform;
			transition-duration: $animation-speed; }

		&::after {
			transform: rotate(0.25turn); } } }

//      
.nav__angle {

	//  ( )
	li.is-open > .nav__tab & i {
		//transform: rotate(0)

		&::before, &::after {
			transform: rotate(0); } }

	// 
	.no-touchevents li > .nav__tab &:hover i {
		&::before, &::after {
			background-color: $base-color; } }

	.no-touchevents .ea-site li > .nav__tab &:hover i {
		&::before, &::after {
			background-color: $base-color--ea; } } }

//     
.nav__drop {

	//       
	//ul, a, .nav__angle
	//ackground-color: $nav-bg-color-s

	a {
		text-transform: uppercase;
		padding-left: round($text-layout-space-horizontal--lg + $text-layout-space-horizontal--lg/2);

		@include media('<tab-lg') {
			padding-left: round($text-layout-space-horizontal--md + $text-layout-space-horizontal--md/2); }

		@include media('<tab-sm') {
			padding-left: round($text-layout-space-horizontal--sm + $text-layout-space-horizontal--sm/2); }

		span {
			letter-spacing: 0.05em; } }

	.nav__drop {
		//       
		//ul, a, .nav__angle
		//ackground-color: $nav-bg-color-ss

		a {
			text-transform: none;
			padding-left: round($text-layout-space-horizontal--lg*2);

			@include media('<tab-lg') {
				padding-left: round($text-layout-space-horizontal--md*2); }

			@include media('<tab-sm') {
				padding-left: round($text-layout-space-horizontal--sm*2); } }

		.nav__drop {
			//       
			//ul, a, .nav__angle
			//ackground-color: $nav-bg-color-sss

			a {
				color: #999;
				padding-left: round($text-layout-space-horizontal--lg*2);

				@include media('<tab-lg') {
					padding-left: round($text-layout-space-horizontal--md*2); }

				@include media('<tab-sm') {
					padding-left: round($text-layout-space-horizontal--sm*2); }

				&::before {
					min-height: 34px; }

				span {
					@include font(14px, 18px); } }

			//.nav__drop
			///       
			///ul, a, .nav__angle
 } } }			//background-color: $nav-bg-color-ssss

/* nav list */

/*navigation end*/
