@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* fotorama */
.fotorama__arr {
	background: transparentize($bg-color-light, 0.2);
	@include size(43px, 44px);

	&::before, &::after {
		@include pseudo();
		@include size(12px, 2px);
		top: 50%;
		border-radius: 0;
		background-color: black; }

	.no-touchevents &:hover {
		background-color: $bg-color-light; } }

.fotorama__arr--prev {
	left: 0;

	&::before, &::after {
		left: 16px; }

	&::before {
		transform-origin: left bottom;
		transform: rotate(-45deg); }

	&::after {
		transform-origin: left top;
		transform: rotate(45deg); } }

.fotorama__arr--next {
	right: 0;

	&::before, &::after {
		right: 16px; }

	&::before {
		transform-origin: right bottom;
		transform: rotate(45deg); }

	&::after {
		transform-origin: right top;
		transform: rotate(-45deg); } }

.fotorama__arr--disabled {
	pointer-events: auto; }

.fotorama__fullscreen-icon {
	@include size(44px);
	top: auto;
	bottom: -1px;
	right: -1px;
	overflow: hidden;
	background: white url(../../img/zoom.svg) no-repeat center;
	background-size: 19px 19px;
	border-radius: 0;
	border: 1px solid $outline-color;
	transition-property: background-color;
	transition-duration: $animation-speed;

	.no-touchevents &:hover {
		background-color: $bg-color-light; }

	.fullscreen & {
		$btn-space: 12px;
		$btn-lines-width: 22px;
		$btn-lines-height: 2px;
		$btn-lines-color: black;

		@mixin line-style() {
			@include size($btn-lines-width, $btn-lines-height); }

		padding: 0;
		top: 20px;
		right: 50%;
		margin-right: -22px;
		background: rgba(255, 255, 255, 0.2);
		border: 2px solid black;
		transition-property: background-color, opacity, visibility;
		transition-duration: $animation-speed;
		z-index: 9999;

		&::before, &::after {
			@include pseudo();
			left: 50%;
			top: 50%;
			margin: -#{round($btn-lines-height*0.5)} 0 0 -#{round($btn-lines-width*0.5)};
			backface-visibility: hidden;
			@include line-style;
			background: $btn-lines-color;
			transition-property: background-color;
			transition-duration: $animation-speed; }

		//first line menu
		&::before {
			transform: rotate(-45deg); }

		//third line menu
		&::after {
			transform: rotate(45deg); } } }

.fotorama__wrap--css3.fotorama__wrap--no-controls.fotorama__wrap--slide.fotorama__wrap--toggle-arrows .fotorama__fullscreen-icon:not(:focus) {
	transform: none;
	opacity: 1; }

.fotorama__wrap--css3 {
	.fotorama__arr:not(:focus),
	.fotorama__fullscreen-icon:not(:focus),
	.fotorama__video-close:not(:focus),
	.fotorama__video-play:not(:focus) {
		transition-property: transform ,opacity, background-color;
		transition-duration: $animation-speed; } }

.fotorama__nav {
	text-align: left;

	.fotorama--fullscreen & {
		text-align: center; } }

.fotorama__stage, .fotorama__thumb {
	border: 1px solid $outline-color; }

.fotorama__thumb {
	background-color: white;
	border-radius: 0; }

.fotorama__thumb-border {
	border-color: $base-color--ea;
	border-radius: 0; }

.fotorama--fullscreen {
	&, & .fotorama__nav, & .fotorama__stage {
		background: white; } }

.p-card__gallery.fotorama--fullscreen .fotorama__thumb-border {
	border-radius: 0; }

/* fotorama end */
