	color: #0099ff;
}

input[type="checkbox"],
input[type="radio"] {
	float: left;
	width: 16px;
	height: 16px;
	padding: 0;
	margin: 0;
	display: none;
}

input[type="checkbox"] ~ label > span,
input[type="checkbox"] ~ span,
input[type="radio"] ~ label > span,
input[type="radio"] ~ span {
	display: inline-block;
	vertical-align: top;
	position: relative;
	padding: 0 0 0 34px;
	z-index: 2;
}

input[type="checkbox"] ~ i,
input[type="radio"] ~ i {
	font-size: 0;
	line-height: 0;
}

input[type="checkbox"] ~ i,
input[type="radio"] ~ i {
	position: absolute;
	left: 0;
	top: 50%;
}

input[type="checkbox"] ~ label::before,
input[type="checkbox"] ~ i::before {
	content: '';
	width: 18px;
	height: 18px;
	position: absolute;
	left: 0;
	top: 50%;
	margin-top: -9px;
	border-radius: 0;
	border: 1px solid black;
}

input[type="checkbox"] ~ label::after,
input[type="checkbox"] ~ i::after {
	content: '';
	width: 16px;
	height: 16px;
	position: absolute;
	left: 1px;
	top: 50%;
	margin-top: -8px;
	border-radius: 0;
	background: none;
	transition-property: opacity;
	transition-duration: 0.2s;
}

input[type="checkbox"] ~ label > span::before, input[type="checkbox"] ~ label > span::after,
input[type="checkbox"] ~ span::before,
input[type="checkbox"] ~ span::after {
	content: '';
	position: absolute;
	width: 0;
	height: 3px;
	left: 0;
	top: 50%;
	border-radius: 2px;
	background: black;
	transition-property: opacity;
	transition-duration: 0.2s;
	transition-timing-function: linear;
}

input[type="checkbox"] ~ label > span::before,
input[type="checkbox"] ~ span::before {
	transform: rotate(45deg);
	transform-origin: 0 7px;
	z-index: 1;
}

input[type="checkbox"] ~ label > span::after,
input[type="checkbox"] ~ span::after {
	transform: rotate(-50deg);
	transform-origin: 0 10px;
	left: 22px;
}

input[type="checkbox"]:checked ~ label > span::before,
input[type="checkbox"]:checked ~ span::before {
	width: 7px;
	margin: -4px 0 0 0;
	transition-property: width;
	transition-duration: 0.1s;
	backface-visibility: hidden;
}

input[type="checkbox"]:checked ~ label > span::after,
input[type="checkbox"]:checked ~ span::after {
	width: 11px;
	margin: 0 0 0 -8px;
	transition-property: width;
	transition-duration: 0.1s;
	transition-delay: 0.1s;
	backface-visibility: hidden;
}

input[type="checkbox"]:disabled ~ label,
input[type="checkbox"]:disabled ~ span {
	color: #bdbdbd;
	cursor: default;
}

input[type="checkbox"]:disabled ~ label::before,
input[type="checkbox"]:disabled ~ i::before {
	border-color: #d6d6d6;
}

input[type="checkbox"]:disabled ~ label > span:before, input[type="checkbox"]:disabled ~ label > span:after,
input[type="checkbox"]:disabled ~ span:before,
input[type="checkbox"]:disabled ~ span:after {
	background: #b9b9b9;
}

input[type="radio"] ~ label::before,
input[type="radio"] ~ span::before {
	content: '';
	width: 18px;
	height: 18px;
	position: absolute;
	left: 0;
	top: 50%;
	margin-top: -9px;
	border-radius: 50px;
	border: 1px solid black;
}

input[type="radio"] ~ label::after,
input[type="radio"] ~ span::after {
	content: '';
	width: 16px;
	height: 16px;
	position: absolute;
	left: 1px;
	top: 50%;
	margin-top: -8px;
	border-radius: 50px;
	background: none;
	transition-property: opacity;
	transition-duration: 0.2s;
}

input[type="radio"] ~ label > span::after,
input[type="radio"] ~ i::after {
	content: '';
	width: 10px;
	height: 10px;
	position: absolute;
	top: 50%;
	left: 4px;
	margin-top: -5px;
	border-radius: 50%;
	overflow: hidden;
	background: black;
	animation: bounceout 0s;
	animation-iteration-count: 1;
	animation-fill-mode: forwards;
	opacity: 0;
}

input[type="radio"]:checked ~ label > span::after,
input[type="radio"]:checked ~ i::after {
	animation: bounce 0.3s;
	animation-iteration-count: 1;
	opacity: 1;
}

@keyframes bounce {
	0% {
		transform: scale(0.8);
		opacity: 0.4;
	}
	50% {
		transform: scale(1.4);
		opacity: 1;
	}
	100% {
		transform: scale(1);
		opacity: 1;
	}
}

@keyframes bounceout {
	0% {
		transform: scale(0);
		opacity: 1;
	}
	50% {
		transform: scale(1.4);
		opacity: 1;
	}
	100% {
		transform: scale(0);
		opacity: 0.4;
	}
}

input[type="radio"]:disabled ~ label,
input[type="radio"]:disabled ~ span {
	color: #bdbdbd;
	cursor: default;
}

input[type="radio"]:disabled ~ label::before,
input[type="radio"]:disabled ~ span::before {
	border-color: #d6d6d6;
}

input[type="radio"]:disabled ~ label > span::after,
input[type="radio"]:disabled ~ i::after {
