	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}

.file-download__icon span:active, .file-download__icon span:focus {
	clip: auto;
	height: auto;
	margin: 0;
	overflow: visible;
	position: static;
	width: auto;
}

.file-download__icon i {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 32px;
	height: 11px;
	border: solid white;
	border-width: 0 2px 2px;
	margin: 5px 0 0 -16px;
	transition-property: transform;
	transition-duration: 0.2s;
}

.file-download__icon i::before, .file-download__icon i::after {
	content: "";
	display: block;
	position: absolute;
	left: 50%;
	bottom: 6px;
	transition-property: bottom;
	transition-duration: 1s;
}

.file-download__icon i::before {
	position: absolute;
	width: 2px;
	height: 20px;
	margin-left: -1px;
	background-color: black;
	z-index: 5;
}

.file-download__icon i::after {
	content: "";
	display: block;
	position: absolute;
	width: 12px;
	height: 12px;
	margin-left: -6px;
	border: solid black;
	border-width: 0 0 2px 2px;
	transform: rotate(-45deg);
}

