	transform: scale(1.5);
}

.no-touchevents .file-download:hover .file-download__icon i::before, .no-touchevents .file-download:hover .file-download__icon i::after {
	animation-name: download;
	animation-duration: 1s;
	animation-iteration-count: infinite;
}

@keyframes download {
	0% {
		bottom: 6px;
	}
	50% {
		bottom: 15px;
	}
	100% {
		bottom: 6px;
	}
}

