	content: attr(data-count);
	margin: -8px 0 0 11px;
	font-size: 12px;
	line-height: 16px;
	opacity: 1;
	visibility: visible;
	transform: translate(0, 0);
	transition-property: opacity, visibility, transform;
	transition-duration: 0.3s;
	transition-delay: 0.1s;
}

