	text-decoration: none;
	transition-property: color;
	transition-duration: 0.2s;
	padding: 0 20px 0 60px;
}

.no-touchevents .side-menu-drop a:hover {
	color: #fabb00;
}

.side-menu-drop a::before, .side-menu-drop a::after {
	content: "";
	display: block;
	position: absolute;
	top: 18px;
	left: 40px;
	pointer-events: none;
	opacity: 0;
	visibility: hidden;
	transition-property: opacity, visibility, transform;
	transition-duration: 0.2s;
}

.side-menu-drop a::before {
	content: "";
	margin-left: -2px;
	margin-top: -3px;
	width: 5px;
	height: 5px;
	border-radius: 3px;
	background-color: #fabb00;
