	visibility: visible;
	transition-delay: 0.2s;
}

.side-menu-drop a > span {
	display: block;
	max-width: 100%;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
	opacity: 0;
	visibility: hidden;
	transform: translate(20px, 0);
	transition-property: opacity, visibility, transform;
	transition-duration: 0.3s;
	transition-delay: 0.1s;
}

