		width: 50%;
		margin-bottom: 40px;
	}
}

@media (max-width: 767px) {
	.c-section-thumb {
		width: auto;
		min-height: 0;
		text-align: left;
		background-color: #102030;
		padding-right: 55px;
		margin-bottom: 0;
		border: none;
	}
	.c-section-thumb::before {
		content: "";
		display: block;
		position: absolute;
		left: 10px;
		top: 10px;
		width: calc(100% - 10px*2);
		height: calc(100% - 10px*2);
		border: 2px solid rgba(255, 255, 255, 0.5);
		transition-property: border-color, border-style;
		transition-duration: 0.2s;
	}
}

.c-section-thumb > span {
	border-bottom: 1px dashed white;
	transition-property: border-color;
	transition-duration: 0.2s;
}

@media (max-width: 767px) {
	.c-section-thumb > span {
		display: inline-block;
		vertical-align: middle;
		width: calc(100% - 40px - 15px - 5px);
		border: none;
	}
}

.c-section-thumb svg {
	display: block;
	margin-right: auto;
	margin-left: auto;
	margin-bottom: 2rem;
	width: 100px;
	height: 100px;
}

.c-section-thumb svg:last-child {
	margin-bottom: 0;
}

@media (max-width: 767px) {
	.c-section-thumb svg {
		display: inline-block;
		vertical-align: middle;
		width: 40px;
		height: 40px;
		margin: 0 15px 0 0;
	}
}

.c-section-thumb path {
	fill: inherit;
}

.c-section-thumb .arr {
	position: absolute;
	left: 50%;
	top: 100%;
	width: 0;
	height: 0;
	z-index: 2;
	border-top: 9px solid white;
	border-left: 9px solid transparent;
	border-right: 9px solid transparent;
	margin-top: 30px;
	opacity: 0;
	visibility: hidden;
	transform: translate(0, -40px) scale(0.8);
	transition-property: opacity, visibility, transform;
	transition-duration: 0.2s;
	transition-timing-function: cubic-bezier(0.65, 0.05, 0.36, 1);
}

.c-section-thumb .arr::before {
	content: "";
	display: block;
	position: absolute;
	left: 50%;
	bottom: 100%;
	width: 1px;
	height: 62px;
	background-color: white;
}

@media (max-width: 767px) {
	.c-section-thumb .arr {
		left: auto;
		right: 20px;
		top: 50%;
		width: 24px;
		height: 24px;
		margin-top: -13px;
		border-radius: 50px;
		opacity: 1;
		visibility: visible;
		transform: translate(0, 0) scale(1);
		border: 1px solid white;
	}
	.c-section-thumb .arr::before, .c-section-thumb .arr::after {
		content: "";
		display: block;
		position: absolute;
		bottom: 7px;
		left: auto;
		backface-visibility: hidden;
		width: 8px;
		height: 1px;
		background-color: white;
		transition-property: background-color;
		transition-duration: 0.2s;
	}
	.c-section-thumb .arr::before {
		right: 50%;
		transform-origin: right bottom;
		transform: rotate(45deg);
	}
	.c-section-thumb .arr::after {
		left: 50%;
		transform-origin: left bottom;
		transform: rotate(-45deg);
	}
}

.no-touchevents .c-section-thumb:hover span {
	border-bottom-color: transparent;
}

.c-section-thumb.active-tab {
	border-color: #fabb00;
}

@media (max-width: 767px) {
	.c-section-thumb.active-tab::before {
		border-color: #fabb00;
		border-style: dotted;
	}
}

.c-section-thumb.active-tab > span {
	border-bottom-color: transparent;
}

.c-section-thumb.active-tab .arr {
	opacity: 1;
	visibility: visible;
	transform: translate(0, 0) scale(1);
}

@media (max-width: 767px) {
	.c-section-thumb.active-tab .arr {
		transform: rotate(0.5turn);
	}
}

/* catalog section thumbs end */
/* catalog section panels */
.c-section-panels {
	position: relative;
	background-color: white;
}

.c-section-panels::before {
	content: "";
	display: block;
	position: absolute;
	left: 50%;
	top: 0;
	width: 0;
	height: 0;
	z-index: 2;
	border-top: 20px solid black;
	border-left: 20px solid transparent;
	border-right: 20px solid transparent;
	margin-left: -20px;
	transition-property: transform;
	transition-duration: 0.2s;
}

.collapsed-all-tab .c-section-panels::before {
	transform: translate(0, -20px);
}

@media (max-width: 767px) {
	.c-section-panels::before {
		content: none;
	}
}

.c-section-panels .c-section-thumb {
	display: none;
}

@media (max-width: 767px) {
	.c-section-panels {
		height: auto !important;
	}
	.c-section-panels .c-section-thumb {
		display: block;
	}
}

.c-section-panel {
	padding: 100px 0;
	opacity: 0;
	visibility: hidden;
}

@media (min-width: 768px) {
	.c-section-panel > div {
		display: block !important;
	}
}

@media (max-width: 639px) {
	.c-section-panel {
		padding-top: 50px;
		padding-bottom: 50px;
	}
}

@media (max-width: 767px) {
	.c-section-panel {
		position: relative !important;
		left: auto !important;
		top: auto !important;
		border: none;
		padding: 0;
		opacity: 1 !important;
		visibility: visible !important;
		z-index: auto !important;
	}
	.c-section-panel > div {
		display: none;
		padding-top: 20px;
		padding-bottom: 20px;
	}
}

/* catalog section panels end */
